namespace $.$$ {
// ============================================================================

export interface $me_list2_pos_intf {
  i: number,
  ofsVer: number,
}

export interface $me_list2_visible_row_intf {
  i: number,
  top: number,
  bottom: number,
}
export type $me_list2_visible_rows_type = Array<$me_list2_visible_row_intf>
export const $me_list2 = <$me_atom2_elem_cnf_intf>{
  prop: {
    firstRowMarginTop: () => 0,
    lastRowMarginBottom: () => 0,
    rowMarginLeft: () => 0,
    pos: () => <$me_list2_pos_intf>{i: 0, ofsVer: 0},
    isReadyData: () => false,
    visible_rows: $me_atom2_prop<$me_list2_visible_rows_type>([],
      () => [] as $me_list2_visible_rows_type,
      // ({val}) => console.log({'visible_rows': val}),
    ),
    needRender: () => true,
  },
  style: {
    border: () => '1px solid red',
  },
  dispatch(dispatch_name: string, dispatch_arg: any) {
    return false
  },
  control: {
    canvas: () => <$me_atom2_control_cnf_intf>{
      dispatch(dispatch_name: string, dispatch_arg: any) {
        if (dispatch_name == 'wheel') {
          const {deltaY, deltaX, clientX, clientY} = dispatch_arg
          const clientRect = a<$me_rect_intf>('.#clientRect')
          if ($me_point_in_rect(clientX, clientY, clientRect)) {
            if (Math.abs(deltaY) > Math.abs(deltaX)) {
              a.update('.ofsVer_delta', val => val + deltaY)
            } else {
              a.update('.ofsHor_delta', val => val + deltaX)
            }
            dispatch_arg.ret = true
          }
          return true
        }
        return false
      },
      event: {
        wheel: p => a.dispatch('', 'wheel', {
          clientX: p.event.clientX,
          clientY: p.event.clientY,
          deltaY: p.event._deltaY,
          deltaX: p.event._deltaX,
          ret: false,
        }).ret,
        wheelTouch: p => a.dispatch('', 'wheel', {
          clientX: p.event.start.touches[0].clientX,
          clientY: p.event.start.touches[0].clientY,
          deltaY: p.event._deltaY,
          deltaX: p.event._deltaX,
          ret: false,
        }).ret,

      },
      prop: {
        '#width': '<.#width',
        '#height': '<.#height',
        needRender: $me_atom2_prop(['<.needRender', '.#width', '.#height'],
          () => true,
          ({val}) => {
            a('<.needRender', val)
          },
        ),
        // $me_atom2_prop_bind('<.needRender'),
      },
      prop_non_render: {
        ofsVer_delta: $me_atom2_prop([], () => 0, ({val}) => {
              // console.log(a('.needRender'))
          if (val) a('.needRender', true)
          // console.warn({val, needRender: a('.needRender')})
        }),
        ofsHor_delta: () => 0,
        isReadyData: $me_atom2_prop(['<.isReadyData'],
          null,
          ({val}) => {
            // console.log({isReadyData: val})
              // console.log(a('.needRender'))
            if (val) a('.needRender', true)
              // console.warn(a('.needRender'))
          },
        ),
        //'<.isReadyData',
        pos: $me_atom2_prop_bind('<.pos'),
        isEnd: () => false,
        isBegin: () => false,
        visible_rows: $me_atom2_prop_bind('<.visible_rows'),
        lastImg: () => null as any,
        last: $me_atom2_prop<HTMLCanvasElement>(['.#width', '.#height', '/.#pixelRatio'],
          ({masters: [width, height, pixelRatio]}) => {
            const result = document.createElement('canvas')
            result.width = width * pixelRatio
            result.height = height * pixelRatio
            return result
          },
        ),
      },
      render: p => {
      const start = performance.now()
        const {ctx, pixelRatio, ctxHeight, ctxWidth} = p

        const ofsVer_delta = a('.ofsVer_delta')
        // console.log({ofsVer_delta, needRender: a<boolean>('.needRender'), isReadyData: a<boolean>('.isReadyData')})
        // if (a<boolean>('.isReadyData') && (ofsVer_delta || a<boolean>('.needRender'))) {
        if (!a<boolean>('.needRender')) {
          const start = performance.now()
          const last = a<HTMLCanvasElement>('.last')
          ctx.drawImage(last, 0, 0, last.width, last.height)
          // console.log('drawImage from last', performance.now() - start)
        } else if (a<boolean>('.isReadyData')) {

          const ofsVer_delta = a('.ofsVer_delta')
          // console.warn({ofsVer_delta})

          const pos = a<$me_list2_pos_intf>('.pos')

          const ofsHor = 0
          const ofsHor_delta = a('.ofsHor_delta')
          const ctxOfsHor = ofsHor * pixelRatio

          // const ofsVer_delta = a('.ofsVer_delta')
          // if (ofsVer_delta == 0) console.warn({ofsVer_delta})
          a('.ofsVer_delta', 0)
          if (!(a<boolean>('.isBegin') && ofsVer_delta < 0 || a<boolean>('.isEnd') && ofsVer_delta > 0)) {
            let visible_rows: $me_list2_visible_rows_type = []
            // console.error('here')

            const ofsVer_from = pos.ofsVer//a('.ofsVer_from')
            const ofsVer = Math.max(ofsVer_from + ofsVer_delta, 0)
            const ctxOfsVer = ofsVer * pixelRatio

            const ctxFirstRowMarginTop = a('<.firstRowMarginTop') * pixelRatio
            const ctxLastRowMarginBottom = a('<.lastRowMarginBottom') * pixelRatio
            let ctxTop_from = ctxFirstRowMarginTop - ofsVer
            let ctxTop_from_from = ctxTop_from

            let i_from = pos.i
            let i_from_from = i_from

            const ctxRowMarginLeft = a('<.rowMarginLeft') * pixelRatio
            let ctxLeft = ctxRowMarginLeft - ctxOfsHor

            let started = false
            const saveStart = (i: number, ctxTop: number) => {
              a('.pos', <$me_list2_pos_intf>{
                i,
                ofsVer: ctxFirstRowMarginTop - ctxTop,
              })
              started = true
            }

            const dispatch: row_dispatch_fn_type = (i, ctxTop, ctxBottom) =>
              a.dispatch('<', 'row', {
                ctx,
                pixelRatio,
                ctxOfsVer,
                ctxOfsHor,
                ctxHeight,
                ctxWidth,
                ctxLeft,
                i,
                ctxTop,
                ctxBottom,
              })

            let need_reverse = ofsVer_delta < 0 && ctxTop_from > 0 && !!i_from
            let after_reverse_fn: after_reverse_fn_type = (i, ctxTop) => {
              ctxTop_from_from = ctxTop
              i_from_from = i
              saveStart(i, ctxTop)
            }
            let after_fn: after_fn_type = (i, ctxTop, ctxBottom) => {
              if (ctxBottom >= 0 && !started) {
                saveStart(i, ctxTop)
              }
            }
            const ret = render_helper({
              pixelRatio,
              ctxHeight,
              ctxFirstRowMarginTop,
              ctxLastRowMarginBottom,
              ctxTop_from,
              i_from,
              need_reverse,
              dispatch,
              after_reverse_fn,
              after_fn,
            })
            const {ctxBottom, isEnd, isBegin} = ret
            visible_rows = ret.visible_rows
            a<boolean>('.isEnd', isEnd)
            a<boolean>('.isBegin', isBegin)

            if (isEnd) {
              const delta = ctxHeight - ctxLastRowMarginBottom - ctxBottom
              if (delta) {
                ctx.clearRect(0, 0, ctxWidth, ctxHeight)

                ctxTop_from = ctxTop_from_from + delta
                i_from = i_from_from
                need_reverse = ctxTop_from > 0
                after_reverse_fn = (i, ctxTop) => {
                  const result = <after_reverse_fn_ret_type>{
                    i_from: ctxTop <= 0 ? i : i + 1,
                    ctxTop_from: ctxTop <= 0 ? ctxTop : ctxFirstRowMarginTop,
                  }
                  saveStart(result.i_from, result.ctxTop_from)
                  return result
                }
                const ret = render_helper({
                  pixelRatio,
                  ctxHeight,
                  ctxFirstRowMarginTop,
                  ctxLastRowMarginBottom,
                  ctxTop_from,
                  i_from,
                  need_reverse: ctxTop_from > 0,
                  dispatch,
                  after_reverse_fn,
                })
                visible_rows = ret.visible_rows
              }
            }
            a('.visible_rows', visible_rows)

            const start = performance.now()
            const last = a<HTMLCanvasElement>('.last')
            const ctxLast = last.getContext('2d')
            ctxLast.clearRect(0, 0, ctxWidth, ctxHeight)
            ctxLast.drawImage(
              ((a.get('<') as $me_atom2_elem).node as HTMLCanvasElement),
              0, 0, ctxWidth, ctxHeight,
            )
            // console.log('drawImage to last', performance.now() - start)
          }
        }
        a('.needRender', false)
        // console.error({needRender: a('.needRender')})
      // console.log('render took', performance.now() - start)
      },
    },
  },
}

type after_reverse_fn_ret_type = {
  i_from: number,
  ctxTop_from: number,
}
type after_reverse_fn_type = (i: number, ctxTop: number) =>
  void | after_reverse_fn_ret_type
type after_fn_type = (i: number, ctxTop: number, ctxBottom: number) =>
  void
type row_dispatch_fn_type = (i: number, ctxTop: number, ctxBottom: number) =>
  row_dispatch_fn_ret_type
type row_dispatch_fn_ret_type = {
  ctxBottom: number,
  ctxTop: number,
}

function render_helper(p: {
  pixelRatio: number,
  ctxHeight: number,
  ctxLastRowMarginBottom: number,
  ctxFirstRowMarginTop: number,
  ctxTop_from: number,
  i_from: number,
  need_reverse: boolean,
  dispatch: row_dispatch_fn_type,
  after_reverse_fn: after_reverse_fn_type,
  after_fn?: after_fn_type,
}) {
  const {
    pixelRatio,
    need_reverse,
    after_reverse_fn,
    after_fn,
    dispatch,
    ctxHeight,
    ctxLastRowMarginBottom,
    ctxFirstRowMarginTop,
  } = p
  let {ctxTop_from, i_from} = p
  const visible_rows: $me_list2_visible_rows_type = []

  let ctxBottom: number
  let ctxTop: number
  let i: number

  let isFirstItem = false
  if (need_reverse) {
    ctxTop = ctxTop_from
    i = i_from - 1
    while (true) {
      const ret = dispatch(i, null, ctxTop).ctxTop
      if (ret == null) {
        isFirstItem = true
        break
      }
      ctxTop = ret
      visible_rows.unshift(<$me_list2_visible_row_intf>{
        i,
        top: ctxTop / pixelRatio,
        bottom: ctxBottom / pixelRatio,
      })
      if (ctxTop <= 0) break
      ctxBottom = ctxTop
      i--
    }
    const ret = after_reverse_fn(i, ctxTop)
    if (ret) {
      ctxTop_from = ret.ctxTop_from
      i_from = ret.i_from
    }
  }
  const isBegin = isFirstItem && ctxTop >= ctxFirstRowMarginTop

  let isLastItem = false
  {
    ctxTop = ctxTop_from
    i = i_from
    while (true) {
      const ret = dispatch(i, ctxTop, ctxBottom).ctxBottom
      if (ret == null) {
        isLastItem = true
        break
      }
      ctxBottom = ret
      visible_rows.push(<$me_list2_visible_row_intf>{
        i,
        top: ctxTop / pixelRatio,
        bottom: ctxBottom / pixelRatio,
      })
      if (after_fn) after_fn(i, ctxTop, ctxBottom)
      if (ctxBottom > ctxHeight) break
      ctxTop = ctxBottom
      i++
    }
  }
  const isEnd = isLastItem && ctxBottom <= ctxHeight - ctxLastRowMarginBottom

  return <render_helper_ret_type>{ctxBottom, isBegin, isEnd, visible_rows}
}

interface render_helper_ret_type {
  ctxBottom: number,
  isBegin: boolean,
  isEnd: boolean,
  visible_rows: $me_list2_visible_rows_type,
}

// ============================================================================
}

