namespace $.$$ {
// ============================================================================

export type $me_atom2_def<Value=number, Masters=any[]> =
  // atom_def_intf |
  $me_atom2_def_apply_intf<Value> |
  $me_atom2_def_compute_intf<Value, Masters> |
  $me_atom2_def_compute_apply_intf<Value, Masters>

interface atom_def_intf
  extends $me_atom2_entity_class_p_base_intf
  {
    keys?: string[],
    is_key?: boolean,
    fn_key_idx_changed?: $me_atom2_key_idx_changed_fn,
    descendant_level?: number,
  }

export interface $me_atom2_def_apply_intf<Value=number>
  extends atom_def_intf
  {
    fn_apply?: $me_atom2_apply_fn<Value>,
  }

export interface $me_atom2_def_compute_intf<Value=number, Masters=any[]>
  extends atom_def_intf
  {
    masters: $me_atom2_def_masters_type,
    fn_compute?: $me_atom2_compute_fn<Value, Masters>,
  }

export interface $me_atom2_def_compute_apply_intf<Value=number, Masters=any[]>
  extends
    $me_atom2_def_apply_intf<Value>,
    $me_atom2_def_compute_intf<Value, Masters>
  {}

// ============================================================================

export class $me_atom2<Value=number, Masters=any> extends $me_atom2_entity {
  private readonly fn_compute?: $me_atom2_compute_fn<Value, Masters>
  private readonly fn_apply?: $me_atom2_apply_fn<Value>
  readonly masters: $me_atom2_masters_type

  private _state: $me_atom2_state_type
  private _value: Value
  // private _value_last_applied: Value
  private _slaves: Map<$me_atom2, string>
  private readonly _descendant_level: number
  private readonly _fn_key_idx_changed: $me_atom2_key_idx_changed_fn
  private _key_provider_cache: $me_atom2_keys_intf
  private _key_gen: $me_atom2<string[]>
  private _masters_store: {
    [name: string]: $me_atom2<any> | string,
  }

  state() { return this._state }
  get 'fn_compute()'() {
    return this._compute()
  }
  get 'fn_apply()'() {
    return this._state === $me_atom2_state_enum.valid ? this._apply(this._value, true) : new Error(`this._state is ${this._state}`)
  }
  get 'update()'() {
    return this.update()
  }
  get 'masters()'() {
    const masters = this._masters()
    if (!masters.length) return null
    const result: {[name: string]: any} = {}
    for (const name_master of masters)
      result[name_master] = this.by_path_s(name_master)
    return result
    // return this._masters().map(name_atom => this.by_path_s(name_atom))
  }
  get 'slaves()'() {
    if (!this._slaves) return null
    const result: {[name: string]: any} = {}
    for (const [atom_slave] of this._slaves)
      result[atom_slave.name()] = atom_slave
    return result
  }
  get 'state()'(): any {
    return this.get_state_helper(this._state)
  }
  private get_state_helper(state: $me_atom2_state_type): any {
    if (state instanceof Error) return state
    if (typeof state === 'number') return $me_atom2_state_enum[state]
    if (state instanceof Set) {
      const state_complex = state as $me_atom2_state_complex
      const result : {[name: string]: any} = {}
      for (const s of state_complex) {
        const ret = this.by_path_s(s)
        const key = typeof ret == 'string' ? ret as string : (ret as $me_atom2).name()
        const val = typeof ret == 'string' ?  'waiting_for' : (ret as $me_atom2)['state()']
        result[key] = val
        // result[key] = val === null ? 'waiting_for' : this.get_state_helper(val)
      }
      return result
    }
  }

  constructor(p: $me_atom2_def<Value>) {
    super({
      ...p,
      ent: p.is_key ?
        $me_atom2_entity_enum.key :
        $me_atom2_entity_enum.prop,
    })
    this._descendant_level = p.descendant_level || 0
    this._fn_key_idx_changed = p.fn_key_idx_changed

    if (p.keys && p.keys.length) {
      const self = this
      const pp = p as $me_atom2_def_compute_intf<Value, Masters>
      this.fn_compute = pp.fn_compute
      const atom = new $me_atom2<string[]>({
        descendant_level: 1,
        tail: '#keys',
        parent: self,
        masters: p.keys.slice(0, 1),
        fn_compute: ({masters: [key]}) => key,
        fn_apply: ({val, prev, atom}) => {
          // if (atom.name() == 'ec.ts:100 /@app@workspace@search@panelResult@grid.@row') console.error(this.name())
          let ss_curr: Set<string>
          let ss_prev: Set<string>
          // console.log(atom.name(), val, prev)
          if (prev) {
            ss_curr = new Set<string>(val)
            for (const tail of prev)
              if (!ss_curr.has(tail)) {
                const atom = this._entities && this._entities.key && this._entities.key[tail] as $me_atom2
                if (atom) {
                  atom.value(null)
                  atom.destroy()
                }
              }
          }
          if (val) {
            ss_prev = new Set<string>(prev)
            // console.warn(ss_prev)
            for (const tail of val)
              if (!ss_prev.has(tail))
                new $me_atom2<Value>({
                  ...p,
                  descendant_level: 1,
                  tail,
                  is_key: true,
                  keys: p.keys.slice(1),
                  parent: self,
                })
          }
          if (val && !val.length && this.path.tail.startsWith('@')) {
            const ec = this.parent() as $me_atom2_ec
            if (ec instanceof $me_atom2_ec) {
              ec._wait_for_child_did(ec.name() + this.path.tail)
            // console.log(`%c${atom.name()}`, 'background: magenta; color: white', ec.name(), ec.name() + this.path.tail, ec, val, ec._wait_for_child)
            } else {
              console.error(ec)
            }
            // console.log(`%c${atom.name()}`, 'background: magenta; color: white', this.parent().name(), this.name(), ec, val)
          }
          if (prev && val) {
            for (const tail of ss_curr) {
              if (ss_prev.has(tail)) {
                const idx_prev = prev.indexOf(tail)
                const idx_curr = val.indexOf(tail)
                if (idx_prev != idx_curr) {
                  // const atom = this.by_path(new $me_atom2_path({ent, tail})) as $me_atom2
                  // console.warn('TODO: replace .by_path() to _entities', atom)
                  const atom = this._entities && this._entities.key && this._entities.key[tail] as $me_atom2
                  if (atom) atom._key_idx_changed({
                    key: [tail],
                    change: {idx_curr, idx_prev, i_key: 0}
                  })
                }
              }
            }
          }
        }
      })
      this._key_gen = atom
    } else {

      this.fn_apply = (p as $me_atom2_def_apply_intf<Value>).fn_apply

      const pp = p as $me_atom2_def_compute_intf<Value, Masters>
      this.fn_compute = pp.fn_compute

      if (pp.masters && (!Array.isArray(pp.masters) || pp.masters.length)) {
        if (Array.isArray(pp.masters)) {
          this.masters = pp.masters as string[]
        } else {
          const atom = new $me_atom2<string[]>({
            descendant_level: 1,
            tail: '#masters',
            parent: this,
            masters: (pp.masters as $me_atom2_masters_prop_intf).masters,
            fn_compute: (p) => {
              const masters = (pp.masters as $me_atom2_masters_prop_intf).fn_compute(p) as string[]
              if (!Array.isArray(masters))
                $me_throw(`atom '${atom.name()}'.fn_compute() expected to return string[] instead of`, masters)
              return masters
            },
            fn_apply: ({val, prev}) => {
              if ($me_equal(val, prev)) return
              if (Array.isArray(prev))
                this.unregister_as_slave(prev)
              this._masters_store = null
            },
          })
          atom.add_slave(this, '#masters')
          this.masters = atom
        }
      }
    }
    this.set_state($me_atom2_state_enum.invalid)

    const name = this.name()
    const sp = $me_atom2._waiting_for_atom_def[name]
    if (!sp) return
    for (const [path, master] of sp) {
      const atom = $me_atom2_entity.root().by_path(path) as $me_atom2
      if (typeof atom !== 'string') this.add_slave(atom, master)
    }
    delete $me_atom2._waiting_for_atom_def[name]
  }
  destroy() {
    if (this._slaves)
      for (let [atom_slave] of this._slaves) {
        this.rm_slave(atom_slave)
      }
    this.unregister_as_slave(this._masters())
    super.destroy()
  }

  by_path_s<Entity=$me_atom2<any>>(s: string, keys?: $me_atom2_keys_intf) {
    // const descendant = this._descendant(this._descendant_level) as $me_atom2_entity
    const descendant = this._descendant(this._descendant_level) as $me_atom2_entity
    if (typeof descendant === 'string') $me_throw(descendant as string)
    return descendant.by_path_s(s, keys || this._key_provider()) as any as Entity
  }
  private no_wait_for_master(name_master: string) {
    if (!(this._state instanceof Set)) return
    const state_complex = this._state as $me_atom2_state_complex
    state_complex.delete(name_master)
    if (state_complex.size) return
    this.set_state($me_atom2_state_enum.invalid)
  }
  private add_slave(atom_slave: $me_atom2<any>, name_master: string) {
    if (!this._slaves) this._slaves = new Map<$me_atom2, string>()
    this._slaves.set(atom_slave, name_master)
    atom_slave.no_wait_for_master(name_master)
    const store = atom_slave._masters_store || (atom_slave._masters_store = {})
    store[name_master] = this
    atom_slave.set_state_slave(name_master,
      this._state instanceof Set ? this._state :
      // this._state === $me_atom2_state_enum.valid || this._state == $me_atom2_state_enum.need_check ? $me_atom2_state_enum.need_check
      $me_atom2_state_enum.invalid
    )
  }
  rm_slave(atom_slave: $me_atom2<any>) {
    if (this._slaves && this._slaves.has(atom_slave)) {
      const name_master = this._slaves.get(atom_slave)
      atom_slave.set_state_slave(name_master, $me_atom2_state_enum.invalid)
      if (atom_slave._masters_store) delete atom_slave._masters_store[name_master]
      this._slaves.delete(atom_slave)
      if (!this._slaves.size) this._slaves = null
    }
  }
  unregister_as_slave(masters: string[]) {
    if (Array.isArray(masters))
      for (const name_master of masters) {
        // console.log(masters, this.name())
        const atom_master = this.by_path_s(name_master) as $me_atom2
        if (typeof atom_master !== 'string') {
          atom_master.rm_slave(this)
        } else {
          const ms = $me_atom2._waiting_for_atom_def
          let s = atom_master as string
          const ss = ms[s]
          if (!ss) continue
          ss.delete(this.path)
          if (!ss.size) delete ms[s]
          this.no_wait_for_master(name_master)
        }
      }
  }
  value(val?: Value | $me_atom2_anim_class<Value>, force = false) {
    if (val === void 0 && this._state === $me_atom2_state_enum.valid) return this._value
    // if (
    //   !this.active() //|| $me_atom2._cyclic_dependency
    // ) return null
    this.update(val, force)
    return (
      // $me_atom2._cyclic_dependency ||
      this._state !== $me_atom2_state_enum.valid ?
        null :
        this._value
    ) as Value
  }
  static is_valid_value(val: any) {
    return !(val == null || Number.isNaN(val))
  }
  static _update_atoms = {
    compute: new Map<$me_atom2_path, boolean>(),
    apply: new Map<$me_atom2_path, boolean>(),
  }
  private update(val?: Value | $me_atom2_anim_class<Value>, force = false) {
  // const start = performance.now()
  // try {
    if (
      // !this.active() ||
      // $me_atom2._cyclic_dependency ||
      val === void 0 && !(
        this._state === $me_atom2_state_enum.invalid ||
        this._state === $me_atom2_state_enum.need_check
      )
    ) return

    const true_set = val !== void 0
    if (val === void 0) {
      const start = performance.now()
      const compute_result = this._compute()
      $me_atom2.compute_timing += performance.now() - start
      $me_atom2.compute_count++
      if (!compute_result) return
      const {ret, state} = compute_result
      if (state !== void 0) {
        this.set_state(state)
        return
      }
      val = ret
    }

    let just_set_anim = false
    let next_value = null as Value
    let anim_to_fini: $me_atom2_anim_intf<Value>
    if (!(val instanceof $me_atom2_anim_class)) {
      next_value = val as Value
    } else {
      const anim = (val as $me_atom2_anim_class<Value>)._anim
      if ($me_atom2.is_valid_value(anim.to)) {
        if (!$me_atom2.is_valid_value(anim.from)) {
          const value = typeof this._state == 'number' ? this._value : null
          anim.from = $me_atom2.is_valid_value(value) ? value : anim.to
        }
        if (just_set_anim = (anim.delay > 0 || !$me_equal(anim.from, anim.to))) {
          $me_atom2.anim_to_play.set(
            this.path,
            <$me_atom2_anim_to_play_intf<Value>>{...anim, value: anim.from}
          )
          $me_atom2.anim_active<Value>(anim, true)
          $me_atom2_async()
        }
        next_value = anim.delay > 0 ? void 0 : anim.from
        if (!just_set_anim)
          anim_to_fini = anim
      }
    }
    if (next_value === void 0) return

    if (!just_set_anim) $me_atom2.anim_stop(this.path)
    this.set_value(next_value, true_set, force)
    if (anim_to_fini) $me_atom2.anim_fini(anim_to_fini, this.path)
  // } finally {
  //   $me_atom2.update_timing += performance.now() - start
  //   $me_atom2.update_count++
  // }
  }
  set_value(next_value: Value, true_set = true, force = false) {
    const prev_value = this._value
    if (
      !true_set && !force &&
      $me_equal(next_value, prev_value)
    ) {
      if (this._state === $me_atom2_state_enum.need_check) {
        this._state = $me_atom2_state_enum.valid
        $me_atom2.did_not_apply = true
        return
      } else {
        // console.warn(this.name(), this._state, next_value)
        this._state = $me_atom2_state_enum.valid
        $me_atom2.did_not_apply = true
        return
      }
    }

    // if (this.name() == '/@app@game@board_canvas^board^cell[h][3].#render')
      // console.log(this.name(), this._state, $me_equal(next_value, prev_value), this.masters, next_value, prev_value)
    next_value = this._apply(next_value, force)

    if (true_set) this._masters_store = null
    const state =
      $me_atom2.is_valid_value(this._value = next_value) ? $me_atom2_state_enum.valid :
      true_set && (this.fn_compute || this.masters) ? $me_atom2_state_enum.invalid :
      `${this.name()} got ${next_value}`
    // if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.log(this.name(), state, this._state)
    if (
      state !== this._state ||
      this._state === $me_atom2_state_enum.valid && (force || !$me_equal(prev_value, next_value))
    )
      this.set_state(state, $me_atom2_state_enum.need_check)
  }
  private _compute() : compute_result_intf<Value> {
    return this.update_helper('compute',
      () => {
        let master_values : any[]
        const fn_compute = this.fn_compute
        let masters: string[]

        if (this.masters) {
          let ma: $me_atom2<string[]>
          masters = Array.isArray(this.masters) ? this.masters : (ma = this.masters as $me_atom2<string[]>).value()
          let state: $me_atom2_state_complex
          if (ma && ma._state !== $me_atom2_state_enum.valid) {
            state = new Set<string>()
            state.add(this.name() + '.#masters')
            return <compute_result_intf<Value>>{state}
          }

          let store = this._masters_store
          if (!store) {
            for (const name_master of masters) {
              const atom_master = this.by_path_s(name_master, this._key_provider()) as $me_atom2
              if (atom_master instanceof $me_atom2) {
                atom_master.add_slave(this, name_master)
              } else {
                const name_atom_waiting_for = atom_master as string
                const ms = $me_atom2._waiting_for_atom_def
                const ss = ms[name_atom_waiting_for] || (ms[name_atom_waiting_for] = new Map<$me_atom2_path, string>())
                ss.set(this.path, name_master)
                if (!state) state =
                  this._state instanceof Set ?
                  this._state as $me_atom2_state_complex :
                  new Set<string>()
                state.add(name_master)
              }
            }
            if (state) {
              return <compute_result_intf<Value>>{state}
            }
            store = this._masters_store
          }
          master_values = []
          let not_ready = false
          for (const name_master of masters) {
            const atom_master = store[name_master] as $me_atom2
            if (!atom_master) {
              if (!state) state = new Set<string>()
              state.add(name_master)
              continue
            }
            let value = atom_master.value()
            master_values.push(value)
            if (atom_master._state !== $me_atom2_state_enum.valid) {
              if (!state) state = new Set<string>()
              state.add(name_master)
            }
          }
          if (state) {
            return <compute_result_intf<Value>>{state}
          }
          if (!fn_compute) {
            return {
              ret: (master_values.length == 1 ? master_values[0] : master_values) as any as Value
            }
          }
        }
        if (!fn_compute) {
          return {ret: void 0}
        }
        const result = <compute_result_intf<Value>>{}
        const key_provider_ret = this._key_provider() || {}
        try {
          const start = performance.now()
          result.ret = fn_compute.call(this, {
            atom: this as $me_atom2<Value>,
            prev: this._value,

            len: !master_values ? 0 : master_values.length,
            masters: master_values as any as Masters,
            ...key_provider_ret,
          })
          if (result.ret === void 0) console.error(this.name(), 'compute returned void 0')
          $me_atom2.pure_compute_timing += performance.now() - start
          $me_atom2.pure_compute_count++
        } catch (e) {
          console.error(e)
          result.state = e
        }
        return result
      },
    )
  }
  private _apply(next_value: Value, force = false) {
    const fn_apply = this.fn_apply
    if (fn_apply && (force || !$me_equal(next_value, this._value))) {
      const prev = this._value
      this._value = next_value
      this.update_helper('apply',
        () => {
          const keys = this._key_provider() || {}
          const ret = fn_apply.call(this, {
            atom: this,
            prev,
            val: next_value,
            ...keys,
          }) as any as Value
          if (ret !== void 0) next_value = ret
          return null
        },
      )
    }
    return next_value
  }
  private update_helper(update_kind: 'apply' | 'compute', fn: $me_fn<compute_result_intf<Value>>): compute_result_intf<Value> {
    const ss = $me_atom2._update_atoms[update_kind]
    if (ss.has(this.path)) {
      console.error(
        ` ${update_kind}-`,
        [this.path.toString()].concat(
          [...$me_atom2._update_atoms[update_kind]]
            .map(item => item[0].toString())
            .reverse()
        )
      )
      return null
    }
    ss.set(this.path, true)
    const prev = a.curr
    a.curr = this
    const ret = fn()
    a.curr = prev
    ss.delete(this.path)
    return ret
  }
  // ===========================

  private _key_provider(): $me_atom2_keys_intf {
    if (this.path.tail === '#masters' || this.path.tail === '#keys')
      return (this.parent() as $me_atom2)._key_provider()
    if (this.path.ent !== $me_atom2_entity_enum.key)
      return null
    if (this._key_provider_cache)
      return this._key_provider_cache
    const result = <$me_atom2_keys_intf>{len_key: 0}
    let atom = this as $me_atom2<any>
    let key_enum: string[][]
    let key: string[]
    let keys: string[]
    while (atom.path.ent === $me_atom2_entity_enum.key) {
      result.len_key++
      if (!key) key = Array<string>()
      if (!keys) keys = Array<string>()
      if (!key_enum) key_enum = Array<string[]>()
      key.unshift(atom.path.tail)
      atom = atom.parent() as $me_atom2<any>
      keys = (atom._key_gen.masters as string[]).concat(keys)
      const key_name = atom._key_gen.masters[0]
      const masters_store = atom._key_gen._masters_store
      if (masters_store) {
        const atom_master = (masters_store[key_name] as $me_atom2<string[]>)
        if (atom_master) {
          key_enum = [atom_master.value()].concat(key_enum)
        } else {
          // TODO: uncomment and test for nl/picker
          // console.error(this.name(), key_name, masters_store)
        }
      } else {
        // console.error(atom._key_gen.name())
      }
    }
    if (result.len_key) {
      result.key = key
      result.keys = keys
      result.key_enum = key_enum
    }
    return (this._key_provider_cache = result)
  }
  // ===========================

  private static _spread_atoms = new Map<$me_atom2<any>, $me_atom2_state_type>()
  // private static _cyclic_dependency = false

  static set_state_count = 0
  set_state(val: $me_atom2_state_type, state2spread?: $me_atom2_state_type) : void {
    // if ($me_atom2._cyclic_dependency) return

    this._state = val

    // if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.warn(this.name(), val, this._state, this.fn_apply)
    // if (
      // this.name() == '/@app@workspace@search@panelResult@grid.ofsHor'
      // ||
     // this.name() == '/@demo@dialog@invisible@row[3]@content@wrapper@item.#hidden'
      // ) console.warn(this.name(), val, this._value)
    if (
      this.fn_apply &&
      (val === $me_atom2_state_enum.invalid || val === $me_atom2_state_enum.need_check)
    ) {
    // if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.log(this.name(), val)
      if (this.path.tail != '#clientRect' || $me_atom2_event_mousemove_last && !$me_atom2_event_mousemove_to_process) {
        $me_atom2.add_to_update(this)
        // $me_atom2.to_update.add(this)
        // $me_atom2_async()
      } else {
        // if ($me_atom2_event_mousemove_last && !$me_atom2_event_mousemove_to_process) {
        const entity = this.parent(true) as $me_atom2_entity
        if (entity instanceof $me_atom2_elem) {
          const elem = entity as $me_atom2_elem
    // if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.log('add_to_update', this.name(), val, entity, '--' + elem.node.style.width + '--', '--' + elem.node.style.height + '--', a('/@app@workspace@search@panelResult@shown.#width'), a('/@app@workspace@search@panelResult@shown.#height'))
          if (
            elem.node.style.width == 'auto' ||
            elem.node.style.height == 'auto' ||
            (() => {
              const prop_width = $me_atom2_entity.root().by_path<$me_atom2>($me_atom2_path.fromString('.#width') as $me_atom2_path)
              const prop_height = $me_atom2_entity.root().by_path<$me_atom2>($me_atom2_path.fromString('.#height') as $me_atom2_path)
              return prop_width && prop_height && (prop_width.value() == null || prop_height.value() == null)
            })
          ) {
            // if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.error(this.name())
            $me_atom2.add_to_update(this)
          } else {
            // console.log(this.name(), this.parent(true))
          }
        }
      }
      // $me_atom2.to_update.add(this.path)
      // $me_atom2_async()
    }
    $me_atom2.set_state_count++

    if (!this._slaves) return

    $me_atom2._spread_atoms.set(this, val)
    for (let [atom_slave, name_master] of this._slaves) {
      // if (this.name() === '/@app@workspace@search@panelResult@shown.#isReady') console.error(this.name(), val, this._state, atom_slave)
      atom_slave.set_state_slave(name_master, state2spread !== void 0 ? state2spread : val)
    }
    $me_atom2._spread_atoms.delete(this)
  }
  private set_state_slave(name_master: string, val: $me_atom2_state_type) {
    if ($me_atom2._spread_atoms.has(this)) {
      // $me_atom2._cyclic_dependency = true
      $me_throw(
        ' spread-',
        [[this.name(), val]].concat([...$me_atom2._spread_atoms].map(item => [item[0].name(), item[1]]).reverse()),
        ...[this as $me_atom2<Value>].concat([...$me_atom2._spread_atoms].map(item => item[0]).reverse()),
      )
    }

// if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.log(this.name(), this._state, name_master, val)
// if (this.name() == '/@demo@dialog@invisible@row[2]@content.id')
    // if (this.name().endsWith(']@content.id')) console.log(this.name(), this._state, name_master, val)
    if (this._state instanceof Set && typeof val == 'number') {
      this.no_wait_for_master(name_master)
    } else if (
      (typeof this._state == 'string' || this._state instanceof Error) && typeof val == 'number'
    ) {
      this.set_state($me_atom2_state_enum.invalid)
    } else if (val instanceof Set || typeof val == 'string' || val instanceof Error) {
      if (!(this._state instanceof Set)) this._state = new Set()
      this._state.add(name_master)
    } else if (
      this._state === $me_atom2_state_enum.valid &&
      (val == $me_atom2_state_enum.need_check || val == $me_atom2_state_enum.invalid)
    ) {
      this.set_state($me_atom2_state_enum.need_check)
    }
    // else if (
    //   this._state === $me_atom2_state_enum.invalid
    // )
// if (this.name() == '/@app@workspace@search@panelResult@shown.#clientRect') console.warn(this.name(), this._state, name_master, val)
// if (this.name() == '/@app@workspace@search@panelResult@grid@header@wrapper@cells.#ofsHor') console.warn(this.name(), this._state, name_master, val)
    // if (this.name().endsWith(']@content.id') && val == $me_atom2_state_enum.valid) console.warn(this.name(), this._state, name_master, val)
  }
  // ===========================
  protected _key_idx_changed(p: $me_atom2_key_idx_changed_p_intf) {
    if ($me_debug) console.log(this,p)
    if (this._entities && this._entities.key) {
      const entities_key = this._entities.key
      for (const k in entities_key) {
        (entities_key[k] as $me_atom2)._key_idx_changed({
          key: p.key.concat(k),
          change: {
            ...p.change,
            i_key: p.change.i_key - 1,
          },
        })
      }
    } else if (this._fn_key_idx_changed) {
      let path = this.path
      let n = 0
      while (path.ent === $me_atom2_entity_enum.key) {
        n++
        path = path.path
      }
      p.change.i_key = n - 1 + p.change.i_key
      this._key_idx_changed_helper(p, this.by_path(path) as $me_atom2, n, [])
    }
  }
  private _key_idx_changed_helper(
    p: $me_atom2_key_idx_changed_p_intf,
    parent: $me_atom2,
    n: number,
    key: string[],
  ) {
    if (n <= p.key.length) {
      this._fn_key_idx_changed({
        key: key.concat(p.key),
        change: p.change,
      })
    } else {
      const entities_key = parent._entities.key
      for (const k in entities_key) {
        this._key_idx_changed_helper(
          p,
          entities_key[k] as $me_atom2,
          n - 1,
          key.concat(k),
        )
      }
    }
  }

  // ===========================

  key_enum() {
    const result = []
    let atom = this as $me_atom2<any>
    while (atom._key_gen) {
      const key_enum = atom._key_gen.value()
      result.push(key_enum)
      // atom = atom.by_path(new $me_atom2_path({ent: $me_atom2_entity_enum.key, tail: key_enum[0]})) as $me_atom2<any>
      atom = atom._entities.key[key_enum[0]] as $me_atom2<any>
    }
    return result
  }

  // ===========================

  protected _on_active() {
    if (this.fn_apply && this._state === $me_atom2_state_enum.invalid) {
      $me_atom2.add_to_update(this)
      // $me_atom2.to_update.add(this)
      // $me_atom2_async()
    }
  }

  // ===========================

  private _masters(): string[] {
    const masters = this.masters
    return (
      Array.isArray(masters) ? masters :
      masters instanceof $me_atom2 ? (masters as $me_atom2<string[]>).value() :
      []
    ) as string[]
  }

  // ===========================

  private static _waiting_for_atom_def = <{
    [name: string]: Map<$me_atom2_path, string>
  }>{}

  // ===========================

  static to_def = Array<$me_atom2_def<any>>()

  // ===========================

  static to_update_first = new Set<$me_atom2<any>>()
  static to_update_second = new Set<$me_atom2<any>>()
  static to_update_third = new Set<$me_atom2<any>>()
  private static _update_order = ['#keys', '#masters', '']
  private static _update_atoms_debug = false
  private static did_not_apply = false
  static add_to_update(atom: $me_atom2<any>) {
    const to_update =
      atom.path.tail == '#keys' ? $me_atom2.to_update_first :
      atom.path.tail == '#masters' ? $me_atom2.to_update_second :
      $me_atom2.to_update_third
    to_update.add(atom)
    $me_atom2_async()
  // if (atom.name() == '/@app@workspace@search@panelResult@grid@row[17].#top') console.warn(atom.name(), atom.parent()._waiting_for)
  }
  static compute_timing = 0
  static compute_count = 0
  static update_timing = 0
  static update_count = 0
  static pure_compute_count = 0
  static pure_compute_timing = 0
  static update_atoms(deadline: number, last_now: number) {
    const pre = performance.now() - last_now
    let count = 0
// const ss : string[] = []
    // $me_atom2.compute_timing = 0
    const start = performance.now()
    while (
      $me_atom2.to_update_first.size ||
      $me_atom2.to_update_second.size ||
      $me_atom2.to_update_third.size
    ) {
      const to_update =
        $me_atom2.to_update_first.size ? $me_atom2.to_update_first :
        $me_atom2.to_update_second.size ? $me_atom2.to_update_second :
        $me_atom2.to_update_third
      for (const atom of to_update) {
        to_update.delete(atom)
        if (
          atom._active &&
          atom._state != $me_atom2_state_enum.valid) {
          atom.update()
// ss.push(atom.name())
          count++
        }
      }
    }
    // if (count) console.log(performance.now() - start, $me_atom2.compute_timing)
// if (ss.length) console.log(ss)
    return [count, pre]
  }

  // ===========================
  static anim_to_play = new Map<$me_atom2_path, $me_atom2_anim_to_play_intf<any>>()
  static anim_start(anim: $me_atom2_anim_to_play_intf, t: number) {
    if (anim.start == null)
      anim.start = anim.progress == null ? t :
        t - anim.delay - Math.min(1, anim.progress) * anim.duration
  }
  static anim_active<Value=number>(anim: $me_prop_anim_intf<Value>, active: boolean) {
    if (!anim.path_active) return
    const atom_active = $me_atom2_entity.root().by_path(anim.path_active) as $me_atom2<boolean>
    if (!atom_active) return

    atom_active.value(active)
  }
  static anim_stop(path: $me_atom2_path) {
    if (!$me_atom2.anim_to_play.has(path)) return
    const anim = $me_atom2.anim_to_play.get(path)
  // console.error(path.toString())
    $me_atom2.anim_to_play.delete(path)
    $me_atom2.anim_active(anim, false)
    $me_atom2.anim_fini(anim, path)
  }
  static anim_fini(anim: $me_atom2_anim_intf<any>, path: $me_atom2_path) {
    if (anim.fini) {
      const atom = $me_atom2_entity.root().by_path(path) as $me_atom2
      if (atom instanceof $me_atom2) {
        const prev = a.curr
        a.curr = atom
        anim.fini()
        a.curr = prev
      }
    }
  }
  // ===========================
  static fn_compute_false() {
    return false
  }
  static fn_compute_true() {
    return true
  }
  static fn_compute_zero() {
    return 0
  }
}
export interface $me_atom2_key_idx_changed_p_intf {
  key: string[],
  change: {
    idx_curr?: number,
    idx_prev?: number,
    i_key?: number,
  },
}
export type $me_atom2_key_idx_changed_fn = (p: $me_atom2_key_idx_changed_p_intf) => void

interface compute_result_intf<Value> {
  ret?: Value | $me_atom2_anim_class<Value>,
  state?: Error | $me_atom2_state_complex,
}

export type $me_atom2_state_type =
  string |
  Error |
  $me_atom2_state_enum |
  $me_atom2_state_complex


export type $me_atom2_state_complex = Set<string>

export enum $me_atom2_state_enum {invalid, valid, need_check}

export type $me_atom2_masters_type =
  string[] | $me_atom2<string[]>


// ==========================================================================

export type $me_atom2_def_masters_type =
  string[] | $me_atom2_masters_prop_intf

export type $me_atom2_masters_prop_intf =
  $me_atom2_prop_def_compute_intf<string[]>

// ==========================================================================

export function $me_atom2_anim<Value=number>(anim: $me_atom2_anim_intf<Value>) {
  return new $me_atom2_anim_class<Value>(anim)
}

export type $me_atom2_anim_fn<Value> =
  (p: {from: Value, to:Value, easing: $me_easing_func, progress: number}) => Value

export interface $me_prop_anim_intf<Value=number> {
  from?: Value,
  delay?: number,
  duration?: number,
  easing?: $me_easing_func,
  fn?: $me_atom2_anim_fn<Value>,
  path_active?: $me_atom2_path,
  fini?: $me_fn<void>,
}

export interface $me_atom2_anim_intf<Value=number>
  extends $me_prop_anim_intf<Value>
  {
    to: Value,
  }

export interface $me_atom2_anim_to_play_intf<Value=number>
  extends $me_atom2_anim_intf<Value>
  {
    value: Value,
    start?: number,
    progress?: number,
  }

export class $me_atom2_anim_class<Value=number> {
  public _anim: $me_atom2_anim_intf<Value>
  constructor(anim: $me_atom2_anim_intf<Value>) {
    this._anim = {
      ...anim,
      delay: anim.delay || 0,
      duration: anim.duration || 200,
      easing: anim.easing || $me_easing.easeInOutQuad,
    }
  }
}

// ==========================================================================

export type $me_atom2_compute_fn<Value=number, Masters=any[]> =
  (p: $me_atom2_compute_fn_p_intf<Value, Masters>) => Value | $me_atom2_anim_class<Value>

export interface $me_atom2_compute_fn_p_intf<Value=number, Masters=any[]>
  extends $me_atom2_keys_intf
  {
    atom: $me_atom2<Value>,
    prev: Value,
    len: number,
    masters: Masters,
  }

export type $me_atom2_apply_fn<Value=number> =
  (p: $me_atom2_apply_fn_p_intf<Value>) => Value | void

export interface $me_atom2_apply_fn_p_intf<Value=number>
  extends $me_atom2_keys_intf
  {
    atom: $me_atom2<Value>,
    prev: Value,
    val: Value,
  }

export interface $me_atom2_keys_intf {
  len_key?: number,
  keys?: string[],
  key?: string[],
  key_enum?: string[][],
}

// ==========================================================================

export interface $me_atom2_a {
  <Value=number>(path_s: string, val?: Value, force?: boolean): Value,
  curr?: $me_atom2_entity,
  root: () => $me_atom2_entity,
  get: (path_s: string) => string | $me_atom2_entity,
  dispatch: <Arg=any>(path: string | $me_atom2_path, dispatch_name?: string, dispatch_arg?: Arg) => Arg,
  update<Value=number>(path_s: string, fn: (val: Value) => Value|$me_atom2_anim_class<Value>, force?: boolean): Value|$me_atom2_anim_class<Value>,
}

export const a: $me_atom2_a = (window as any).a = Object.assign(
  <Value=number>(path_s: string, val?: Value, force?: boolean): Value => {
    const relative_to = ((a as any).curr as $me_atom2_entity) || $me_atom2_entity.root()
    const atom = relative_to.by_path_s(path_s) as $me_atom2<Value>
    if (typeof atom === 'string') $me_throw(`atom '${atom}' does not exist`)
    if (!(atom instanceof $me_atom2)) $me_throw(`entity '${atom}' is not $me_atom2`, atom)
    return atom.value(val, force)
  },
  {
    root: () => $me_atom2_entity.root(),
    get(path_s: string) {
      const relative_to = ((a as any).curr as $me_atom2_entity) || $me_atom2_entity.root()
      return !path_s ? relative_to : relative_to.by_path_s(path_s)
    },
    dispatch<Arg=any>(path: string | $me_atom2_path, dispatch_name?: string, dispatch_arg?: Arg) {
      const entity =
        path instanceof $me_atom2_path ? $me_atom2_entity.root().by_path(path as $me_atom2_path) :
        path as string ? a.get(path as string) as $me_atom2_entity :
        a.curr instanceof $me_atom2_ec ? a.curr :
        (a.curr as $me_atom2).parent(true)
      if (typeof entity == 'string') $me_throw(`${entity} does not exist`)
      if (!(entity instanceof $me_atom2_ec)) $me_throw(`${entity.name()} is not $me_atom2_ec`, entity)
      const ec = entity as $me_atom2_ec
      if (!dispatch_name) dispatch_name = a.curr.name()
      const ret = ec.dispatch(dispatch_name, dispatch_arg)
      if (!ret) $me_throw(`failed dispatch("${dispatch_name}", ${dispatch_arg}) on ${ec.name()}`)
      return dispatch_arg
    },
    update<Value=number>(path_s: string, fn: (val: Value) => Value|$me_atom2_anim_class<Value>, force?: boolean): Value|$me_atom2_anim_class<Value> {
      const relative_to = ((a as any).curr as $me_atom2_entity) || $me_atom2_entity.root()
      const atom = relative_to.by_path_s(path_s) as $me_atom2<Value>
      if (typeof atom === 'string') $me_throw(`atom '${atom}' does not exist`)
      if (!(atom instanceof $me_atom2)) $me_throw(`entity '${atom}' is not $me_atom2`, atom)
      const val = atom.value()
      const ret = fn(val)
      if (ret === void 0) $me_throw('update returned undefined for val', val)
      atom.value(ret, force)
      return ret
    },
  }
)

// ==========================================================================
}
