export interface $me_atom2_path_class_p_intf {
  ent: $me_atom2_entity_enum,
  tail?: string,
  path?: $me_atom2_path,
}

export class $me_atom2_path {
  readonly ent: $me_atom2_entity_enum
  readonly tail: string
  readonly path: $me_atom2_path
  constructor (p: $me_atom2_path_class_p_intf) {
    this.ent = p.ent
    const isRoot = p.ent === $me_atom2_entity_enum.root
    this.tail = isRoot ? '' : p.tail
    this.path = isRoot ? null : p.path
  }
  get 'toString()'() {
    return this.toString()
  }
  static fromString(s: string, pos = 0, keys?: $me_atom2_keys_intf): $me_atom2_path | string {
    let idx_key = 0
    const err = (msg: string) => `${msg} in '${s}' at pos ${pos}`
    let path: $me_atom2_path
    let ent: $me_atom2_entity_enum
    while (pos < s.length) {
      const ent_prev = ent
      let ch = s.slice(pos, pos + 1)
      ent = $me_atom2_path_ent2prefix.indexOf(ch) as $me_atom2_entity_enum
      const expected =
        ent_prev == $me_atom2_entity_enum.prop && (
          ent === $me_atom2_entity_enum.control ||
          ent === $me_atom2_entity_enum.elem
        ) ? `"${$me_atom2_path_ent2prefix[$me_atom2_entity_enum.prop]}"` :
        ent < 0 ||
        ent === $me_atom2_entity_enum.root && pos ||
        ent === $me_atom2_entity_enum.key ? $me_atom2_path_ent2prefix
          .filter((val, idx) => idx > $me_atom2_entity_enum.key)
          .reduce((result, val, idx, arr) =>
            result +
            (
              !idx ? '' :
              idx < arr.length - 1 ? `, ` :
              ` or `
            ) +
            `"${val}"`
          , '') :
          ''
      if (expected)
        return err(`${expected} is expected instead of "${ch}"`)
      pos++
      let pos_next = indexOf(s, pos, ...$me_atom2_path_ent2prefix, ']')
      let tail = s.slice(pos, pos_next)
      if (!tail && ent !== $me_atom2_entity_enum.root) {
        if (ent === $me_atom2_entity_enum.prop && (
          (ch = s.slice(pos_next, pos_next + 1)) == $me_atom2_path_ent2prefix[$me_atom2_entity_enum.control] ||
          ch == $me_atom2_path_ent2prefix[$me_atom2_entity_enum.elem]
        )) {
          tail = s.slice(pos, pos_next = indexOf(s, pos_next + 1, ...$me_atom2_path_ent2prefix, ']'))
        } else {
          return err(`${$me_atom2_entity_enum[ent]} name must not be empty`)
        }
      }
      pos = pos_next
      path = new $me_atom2_path({ent, tail, path})
      if (pos < s.length) {
        ch = s.slice(pos, pos + 1)
        if (ch === ']') {
          return err(`"]" without preceding "[" found`)
        } else
          while (ch === '[') {
            const pos_next = indexOf(s, pos + 1, ']')
            if (pos_next === s.length)
              return err(`not found "]" for "["`)

            let tail: string
            if (pos_next - pos >= 2) {
              tail = s.slice(pos + 1, pos_next)
              // const firstChar = tail.charAt(0)
              // if ((firstChar == '-' || firstChar == '+') && Number.isInteger(+tail)) {
              //   if (!keys) return err(`keys must be provided`)
              //   if (idx_key >= keys.key.length) return err(`keys[${idx_key}] must be provided`)
              //   const idx = keys.key_enum[idx_key].indexOf(keys.key[idx_key]) + +tail
              // console.warn(keys.key_enum[idx_key].indexOf(keys.key[idx_key]), {idx_key, tail})
              //   if (idx < 0 || idx >= keys.key_enum[idx_key].length)
              //     return err(`[${tail} => ${idx}] is out of range: [0, ${keys.key_enum[idx_key].length - 1}]`)
              // }
              // if (tail.)
              // if (tail == '-1') {
              //   console.log(tail, keys)
              // }
              // // TODO: [-1], [+1], [0:], [1:-1]
            } else {
              if (!keys) return err(`keys must be provided`)
                // console.log(keys.key)
              if (idx_key >= keys.key.length) return err(`keys[${idx_key}] must be provided`)
              tail = keys.key[idx_key++]
            }
            const ent = $me_atom2_entity_enum.key
            path = new $me_atom2_path({ent, tail, path})
            pos = pos_next + 1
            if (pos >= s.length) break
            ch = s.slice(pos, pos + 1)
          }
      }
    }
    return path
  }
  toString(...paths: $me_atom2_path[]) {
    paths.push(this)
    const arr = Array<{ent: $me_atom2_entity_enum, tail: string}>()
    for (let path of paths.reverse()) {
      arr.push(path)
      while (path = path.path) arr.push(path)
    }
    const result = arr
      .reverse()
      .reduce((result, item, idx) =>
        result +
        $me_atom2_path_ent2prefix[item.ent] +
        item.tail +
        ent2suffix[item.ent] +
        ''
      , '')
    return result
  }
}

export const $me_atom2_path_ent2prefix = ['/', '[', '.', '^', '@']
const ent2suffix = ['', ']', '', '', '']
export const $me_atom2_path_ch_parent = '<'

function indexOf(s: string, from: number, ...ch_seek_for: string[]) {
  const len = s.length
  const len_ch_seek_for = ch_seek_for.length
  for (let i = from; i < len; i++) {
    const ch = s.slice(i, i + 1)
    for (let j = 0; j < len_ch_seek_for; j++)
      if (ch === ch_seek_for[j])
        return i
  }
  return len
}

