namespace $.$$ {
// ============================================================================

export enum $me_atom2_pinch_mode {justStarted, init, move, fini }

// const pinch_threshold = .01

export class $me_atom2_pinch_class {
  mode: $me_atom2_pinch_mode
  _start: TouchEvent
  _end: TouchEvent
  clientX0: number
  clientY0: number

  clientX1: number
  clientY1: number

  distInitial: number
  distCurrent: number
  distPrev: number
  center: $me_point_intf

  start(event: TouchEvent) {
    this.mode = $me_atom2_pinch_mode.justStarted
    this._start = event

    this.clientX0 = event.touches[0].clientX
    this.clientY0 = event.touches[0].clientY

    this.clientX1 = event.touches[1].clientX
    this.clientY1 = event.touches[1].clientY

    this.distInitial = Math.hypot(this.clientX0 - this.clientX1, this.clientY0 - this.clientY1)
    // this.distPrev = this.distInitial
  }
  accum(event: TouchEvent) {
    this.clientX0 = event.touches[0].clientX
    this.clientY0 = event.touches[0].clientY

    this.clientX1 = event.touches[1].clientX
    this.clientY1 = event.touches[1].clientY
  }
  move(event: TouchEvent) {
    this._end = event
    this.center = {
      x: (this.clientX0 + this.clientX1) / 2,
      y: (this.clientY0 + this.clientY1) / 2,
    }
    this.distCurrent = Math.hypot(this.clientX0 - this.clientX1, this.clientY0 - this.clientY1)
    this.clientX0 = event.touches[0].clientX
    this.clientX1 = event.touches[1].clientX
    this.clientY0 = event.touches[0].clientY
    this.clientY1 = event.touches[1].clientY

    if (this.mode == $me_atom2_pinch_mode.justStarted) {
      this.mode = $me_atom2_pinch_mode.init
    }
    // this.mode =
    //   this.mode == $me_atom2_pinch_mode.justStarted ?
    //     $me_atom2_pinch_mode.init :
    //     $me_atom2_pinch_mode.move
    $me_atom2_async()
  }
  cancel() {
    this.mode = null
  }
  end(event: TouchEvent) {
    this.mode = $me_atom2_pinch_mode.fini
    $me_atom2_async()
  }
  raf(t: number) {
    if ($me_atom2_pinch.mode == $me_atom2_pinch_mode.fini && this.distCurrent == null) {
      this.distCurrent = this.distPrev
    }
    const result =
      this.mode == null ||
      // $me_atom2_pinch.mode != $me_atom2_pinch_mode.fini && (
        this.distCurrent == null ||
        // Math.abs(this.distPrev - this.distCurrent) < pinch_threshold * this.distInitial ||
        false
      // )
?
        null :
        <$me_atom2_event_pinch_intf>{
          start: this._start,
          end: this._end,
          distInitial: this.distInitial,
          distCurrent: this.distCurrent,
          scale: this.distCurrent / this.distInitial,
          center: this.center,
        }
    if (result)
      this.distPrev = this.distCurrent
    // console.log('raf', result)

    return result
  }
}

export let $me_atom2_pinch: $me_atom2_pinch_class

// ==========================================================================
}
