namespace $.$$ {
// ============================================================================

type opened_type = Map<string, Map<string, any>>

export const $me_list2_demo = (rootElem: HTMLElement) => {
  return new $me_atom2_elem({tail: 'app', cnf: {
    node: rootElem,
    style: {
      margin: () => 0,
    },
    prop: {
      demo_border: () => 'red',
    },
    elem: {
      demo: () => <$me_atom2_elem_cnf_intf>{
        base: $me_list2,
        prop: {
          '#width': $me_atom2_prop_either(['.isTouch'],
            () => 600,
            () => 300,
          ),
          marginVer: () => 20,
          '#height': $me_atom2_prop(['/.#viewportHeight', '.marginVer'],
            ({masters: [viewportHeight, marginVer]}) => viewportHeight - 2 * marginVer,
          ),
          '#alignHor': () => $me_align.center,
          '#alignVer': () => $me_align.center,
          data: $me_atom2_prop(['/.scheme_metro'],
            ({masters: [scheme_metro]}) => {
              let result = {} as any
              const process_segment = (result: any, segment: any) => {
                if (segment.points) {
                  for (const point_id in segment.points) {
                    result[point_id] = {}
                  }
                }
              }
              const process_line = (result: any, line: any) => {
                const segments = line.segments
                if (!segments) {
                  process_segment(result, line)
                } else {
                  for (const segment_id in segments)
                    process_segment(result[segment_id] = {}, segments[segment_id])
                }
              }
              for (const line_id in scheme_metro)
                if (line_id != 'settings')
                  process_line(result[line_id] = {}, scheme_metro[line_id])
              return result
            },
            ({val}) => {
              a('.isReadyData', true)
            },
          ),
          firstRowMarginTop: () => 8,
          lastRowMarginBottom: () => 8,
          isTouch: '/.#isTouch',
          row_height: $me_atom2_prop_either(['.isTouch'],
            () => 48,
            () => 32,
          ),

          fontSize: $me_atom2_prop_either(['.isTouch'],
            () => 24,
            () => 16,
          ),
          fontFamily: () => '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"', // https://medium.com/needmore-notes/using-system-fonts-for-web-apps-bf76d214a0e0
          fontWeight: () => 400,
          colorText: () => '#313745',
          paddingLeft: () => 8,
          opened: $me_atom2_prop_store({
            default: () => new Map<string, opened_type>(),
            valid: (val: any) => val instanceof Map ? val : null,
            toJSON: val => {
              const mapAsArray = (m: opened_type): Array<[string, Array<any>]> => {
                const result: Array<[string, Array<any>]> = []
                for (const [key, sub] of [...m])
                  result.push([key, mapAsArray(sub)])
                return result
              }
              return mapAsArray(val)
            },
            fromJSON: val => {
              const arrayAsMap = (arr: [string, Array<any>]): opened_type => {
                const result = new Map<string, opened_type>()
                for (const [key, sub] of arr)
                  result.set(key, arrayAsMap(sub))
                return result
              }
              return arrayAsMap(val)
            },
          }),
          pos: $me_atom2_prop_store({
            default: () => <$me_list2_pos_intf>{i: 0, ofsVer: 0},
            valid: (val: any) => val && typeof val.i == 'number' && typeof val.ofsVer == 'number' ? val : null,
          }),
        },
        event: {
          clickOrTap: p => {
            const clientX =
              p.event.start instanceof MouseEvent ?
                p.event.start.clientX :
                p.event.start.touches[0].clientX
            const clientY =
              p.event.start instanceof MouseEvent ?
                p.event.start.clientY :
                p.event.start.touches[0].clientY
            if (!p.isInRect(clientX, clientY)) return false
            const clientRect = a<$me_rect_intf>('.#clientRect')
            const y = clientY - clientRect.top
            const visible_rows = a<$me_list2_visible_rows_type>('.visible_rows')
            for (let j = 0; j < visible_rows.length; j++) {
              const {top, bottom, i} = visible_rows[j]
              if (top <= y && y < bottom) {
                let data = a<any>('.data')
                let opened_orig = a<opened_type>('.opened')
                a.dispatch('', 'click', {
                  i,
                  idx: idx_by_i(
                    i,
                    a<any>('.data'),
                    a<opened_type>('.opened'),
                  ),
                })
                break
              }
            }
            return true
          },
        },
        dispatch(dispatch_name: string, dispatch_arg: any) {
          if (dispatch_name == 'click') {
            const {idx} = dispatch_arg
            let data = a<any>('.data')
            let opened_orig = a<opened_type>('.opened')

            let opened = opened_orig
            let changed = false
            for (let j = 0; j < idx.length && !changed; j++) {
              const key = Object.keys(data)[idx[j]]
              const isTerm = !Object.keys(data[key]).length
              if (isTerm) break
              const isLast = j == idx.length - 1
              const isOpened = opened.has(key)
              if (!(changed = !isOpened || isLast)) {
                opened = opened.get(key)
                data = data[key]
              } else if (isOpened) {
                opened.delete(key)
              } else {
                opened.set(key, new Map())

                const atom = a.get('^canvas.ofsVer_delta') as $me_atom2
                a('^canvas.isEnd', false)
                const delta = row_height(idx.length + 1)
                let delta_rem = delta
                const duration = 100
                const interval = 8
                const start = performance.now()
                const timer = setInterval(() => {
                  const progress = $me_easing.easeOutQuad(
                    Math.min(1, (performance.now() - start) / duration)
                  )
                  const delta_target = delta * progress
                  const delta_rem_new = delta - delta_target
                  const gamma = Math.max(0, (delta_rem - delta_rem_new))
                  delta_rem = delta_rem_new
                  const value_new = atom.value() + gamma
                  if (value_new) atom.value(value_new)
                  if (progress >= 1) clearInterval(timer)
                }, interval)
              }
            }

            if (changed) {
              a('.needRender', true)
              console.log(a('.needRender'))
              a<opened_type>('.opened', opened_orig, true)
            }
            return true

          } else if (dispatch_name == 'row') {
            const {ctx, pixelRatio, ctxLeft, ctxOfsVer, ctxOfsHor, i, ctxHeight, ctxWidth} = dispatch_arg
            const idx = idx_by_i(i, a<any>('.data'), a<Map<string, any>>('.opened'))

            if (!idx) {
              dispatch_arg.ctxTop = null
              dispatch_arg.ctxBottom = null
              return true
            }

            let {ctxTop, ctxBottom} = dispatch_arg
            const idx_len_max = 3
            const ctxRowHeightMin = row_height(idx_len_max) * pixelRatio
            const ctxRowHeight = row_height(idx.length) * pixelRatio
            if (ctxTop == null) {
              dispatch_arg.ctxTop = ctxTop = ctxBottom - ctxRowHeight
            } else {
              dispatch_arg.ctxBottom = ctxBottom = ctxTop + ctxRowHeight
            }

            const ctxIndent = 16 * pixelRatio
            $me_atom2_ctx_rect({
              ctx,
              ctxTop,
              ctxLeft,
              ctxWidth,
              ctxHeight: ctxRowHeight,
              stroke: {
                ctxWidth: 1,
                style: 'silver',
              },
            })
            ctx.textAlign = 'left'
            ctx.textBaseline = 'bottom'
            ctx.fillStyle = a('.colorText')

            let ctxFontSize = fontSize() * pixelRatio//(a('.fontSize')) * pixelRatio
            ctx.font = a('.fontWeight') + ' ' + ctxFontSize + 'px ' + a('.fontFamily')
            const ctxWidthLineNo = ctx.measureText(i + Math.ceil(ctxHeight / ctxRowHeightMin) + ':').width

            ctxFontSize = fontSize(idx.length) * pixelRatio
            ctx.font = a('.fontWeight') + ' ' + ctxFontSize + 'px ' + a('.fontFamily')

            ctx.fillText(
              i + ':',
              ctxLeft + a('.paddingLeft') * pixelRatio,
              ctxTop + (ctxRowHeight + ctxFontSize) / 2,
            )

            let data = a<any>('.data')
            let text = ''
            for (let j = 0; j < idx.length; j++) {
              const key = Object.keys(data)[idx[j]]
              if (j < idx.length - 1) {
                data = data[key]
              } else {
                text = key
              }
            }

            ctx.fillText(
              text,
              ctxLeft + a('.paddingLeft')* pixelRatio + ctxWidthLineNo + ctxIndent * idx.length,
              ctxTop + (ctxRowHeight + ctxFontSize) / 2,
            )
            return true
          }
          return false
        },
      }
    }
  }})
}

// ==========================================================================

function fontSize(idx_len: number = 0) {
  return a('.fontSize') - 1*(idx_len - 1)
}

function row_height(idx_len: number) {
  return a('.row_height') - 1*(idx_len - 1)
}

// ==========================================================================

function idx_by_i(i: number, data: any, opened: opened_type): number[] {
  let level = [{data, opened}] as Array<{data: any, opened: opened_type}>
  let idx = [0] as number[]
  let j = 0
  LOOP: for (j = 0; j < i; j++) {
    let top = level.length-1
    let {data, opened} = level[top]
    // console.log({data, opened})
    const key = Object.keys(data)[ idx[top] ]
    if (opened && opened.has(key)) {
      level.push({data: data[key], opened: opened.get(key)})
      idx.push(0)
    } else {
      while (idx[top] >= Object.keys(data).length - 1) {
        level.pop()
        idx.pop()
        if (!level.length) break LOOP
        data = level[--top].data
      }
      idx[top]++
    }
  }
  const result =  i != j ? null : idx
  return result
}

// ==========================================================================

($me_atom2_entity.root() as $me_atom2_entity).props({
  'scheme_metro': $me_atom2_prop([],
    () => 'requires data',
    ({val}) => {
      if (val != 'requires data') return val

      const idx = window.location.pathname.indexOf('/', 1)
      // console.log(window.location.origin.includes('localhost'))
      const root = window.location.origin.includes('localhost') ? '/nl' : '/newlook'
        // !~idx ?
        //   window.location.pathname :
        //   window.location.pathname.slice(0, idx)
      const worker = new Worker(window.location.origin + root + '/metro/-/web.js')
      const store_key = 'msk-metro-guids'
      worker.postMessage({cmd: 'prepare', guids: localStorage.getItem(store_key)})
      worker.onmessage = (event: MessageEvent) => {
        const cmd = event.data.cmd
        const data = event.data.data
        if (cmd == 'data') {
          a('/.scheme_metro', data.scheme)
          a('/.guid2point', data.guid2point)
          a('/.code2guids', data.code2guids)
          if (data.guids)
            localStorage.setItem(store_key, data.guids)
        }
      }

      return null
    }
  ),
  'guid2point': () => null as any,
  'code2guids': () => null as any,
})

// ==========================================================================
}