export const $me_atom2_prop_compute_fn_or = (initial?: boolean) =>
  initial === void 0 ? compute_fn_or :
  (p: $me_atom2_compute_fn_p_intf<boolean, boolean[]>) =>
    compute_fn_and(p, initial)
const compute_fn_or = (p: $me_atom2_compute_fn_p_intf<boolean, boolean[]>, initial?: boolean) =>
    p.masters.reduce(
      (result, val) => result || val,
      initial === void 0 ? false : initial
    )

export const $me_atom2_prop_compute_fn_and = (initial?: boolean) =>
  initial === void 0 ? compute_fn_and :
  (p: $me_atom2_compute_fn_p_intf<boolean, boolean[]>) =>
    compute_fn_and(p, initial)
const compute_fn_and = (p: $me_atom2_compute_fn_p_intf<boolean, boolean[]>, initial?: boolean) =>
    p.masters.reduce(
      (result, val) => result && val,
      initial === void 0 ? true : initial
    )

export const $me_atom2_prop_compute_fn_mul = (initial?: number) =>
  initial === void 0 ? compute_fn_mul :
  (p: $me_atom2_compute_fn_p_intf<number, number[]>) =>
    compute_fn_mul(p, initial)
const compute_fn_mul = (p: $me_atom2_compute_fn_p_intf<number, number[]>, initial?: number) =>
    p.masters.reduce(
      (result, val) => result * val,
      initial === void 0 ? 1 : initial
    )

export const $me_atom2_prop_compute_fn_sum = (initial?: number) =>
  initial === void 0 ? compute_fn_sum :
  (p: $me_atom2_compute_fn_p_intf<number, number[]>) =>
    compute_fn_sum(p, initial)
const compute_fn_sum = (p: $me_atom2_compute_fn_p_intf<number, number[]>, initial?: number) =>
    p.masters.reduce(
      (result, val) => result + val,
      initial === void 0 ? 0 : initial
    )

export const $me_atom2_prop_compute_fn_diff = (initial?: number) =>
  initial === void 0 ? compute_fn_diff :
  (p: $me_atom2_compute_fn_p_intf<number, number[]>) =>
    compute_fn_diff(p, initial)
const compute_fn_diff = (p: $me_atom2_compute_fn_p_intf<number, number[]>, initial?: number) =>
    p.masters.reduce(
      (result, val, idx) => result + (!idx ? val : -val),
      initial === void 0 ? 0 : initial
    )

