export const $me_atom2_prop_keys = (masters: $me_atom2_prop_masters_type, strip_null = false) =>
  $me_atom2_prop<string[], [{[key: string]: any}]>(masters,
    ({masters}) => Object.keys(masters[0]).filter((key: string) => !strip_null || masters[0][key] != null),
  )

export const $me_atom2_prop_bind = (master: string) =>
  $me_atom2_prop([master],
    null,
    ({val}) => { a(master, val, true) }
  )

export const $me_atom2_prop_either = <Value>(
  masters: $me_atom2_prop_masters_type,
  ifTrue: $me_atom2_compute_fn<Value>,
  ifFalse: $me_atom2_compute_fn<Value>,
) =>
  $me_atom2_prop<Value, [boolean]>(masters,
    ({atom, prev, key, len, masters}) =>
      !len ?
        $me_throw(`non empty masters must be specified for ${atom.name()}`) as any as Value :
      masters[0] ?
        ifTrue({atom, prev, key, len: len - 1, masters: masters.slice(1)}) :
        ifFalse({atom, prev, key, len: len - 1, masters: masters.slice(1)})
  )

