export function $me_atom2_prop_same_def<Value=number>(
  prop_def: $me_atom2_prop_def<Value>,
  props: string[]
) {
  const result = <$me_atom2_prop_intf>{}
  for (const prop of props) result[prop] = prop_def
  return result
}

export function $me_atom2_prop_same_fn_compute<Value=number>(
  fn_compute: $me_atom2_compute_fn<Value>,
  props: {[prop: string]: $me_atom2_prop_masters_type},
  fn_apply_props?: {[prop: string]: $me_atom2_apply_fn<Value>},
) {
  const result = <$me_atom2_prop_intf>{}
  for (const prop in props)
    result[prop] = $me_atom2_prop(props[prop], fn_compute, fn_apply_props && fn_apply_props[prop])
  return result
}

export function $me_atom2_prop_cascade<Value=number>(prop_def: $me_atom2_prop_def<Value>, prop: string, cascade_def: string | any[]) {
  const result = <$me_atom2_prop_intf>{}
  cascade_helper(result, prop_def, prop, cascade_def)
  return result
}

function cascade_helper<Value>(result: $me_atom2_prop_intf, prop_def: $me_atom2_prop_def<Value>, prop: string, cascade_def: string | any[]) {
  result[prop] = prop_def
  if (typeof cascade_def === 'string') {
    result[cascade_def] = '.' + prop
  } else if (Array.isArray(cascade_def)) {
    const arr = cascade_def as any[]
    for (let i = 0; i < arr.length; i++) {
      const item = arr[i]
      if (typeof item === 'string') {
        result[item] = '.' + prop
      } else if (Array.isArray(item)) {
        const item_as_arr = item as any[]
        if (!item_as_arr.length) $me_throw(`arr[${i}] is empty Array`, arr)
        if (typeof item_as_arr[0] != 'string') $me_throw(`arr[${i}][0] is not string`, arr)
        if (item_as_arr.length == 1) {
          result[item_as_arr[0]] = '.' + prop
        } else {
          if (item_as_arr.length > 2) $me_throw(`arr[${i}] is too long Array: length (${item_as_arr.length}) > 2`, arr)
          if (typeof item_as_arr[1] != 'string' && !Array.isArray(item_as_arr[1])) $me_throw(`arr[${i}][1] is nor string, neither Array, but ${item_as_arr[1]}`, arr)
          cascade_helper<Value>(result, '.' + prop, item_as_arr[0] as string, item_as_arr[1] as string | any[])
        }
      }
    }
  } else $me_throw('cascade_def is nor string, neither Array', cascade_def)
}

