export const $me_atom2_prop_store = <Value=number, Masters=[boolean, Value]>(p: {
  default: () => Value,
  valid: (val: any) => Value,
  toJSON?: (val: Value) => any,
  fromJSON?: (val: any) => Value,
  is_equal?: (val_a: Value, val_b: Value) => boolean,
  condition?: string[],
}) => {
  const atom = $me_atom2_prop<Value>(!p.condition ? [] : p.condition,
    ({atom, masters, prev, len}) => {
      let val = null as any
      const name = atom.name()
      if (len && !masters[0]) {
        val = masters[1] || prev || null
        sessionStorage.removeItem(name)
      } else {
        const s = sessionStorage.getItem(name)
        if (s)
          try {
            val = !p.fromJSON ? JSON.parse(s) : p.fromJSON(JSON.parse(s))
          } catch (e) {
            console.error({name}, e)
          }
      }
      return val
    },
    ({val, atom}) => {
      // if (atom.name().endsWith('.selected')) console.warn(val)
      val = p.valid(val)
      let need_remove = false
      const dflt = p.default()
      if (val == null) {
        val = dflt
        need_remove = true
      } else if (p.is_equal ? p.is_equal(val, dflt) : $me_equal(val, dflt)) {
        need_remove = true
      }

      const name = atom.name()
      if ( need_remove ) {
        sessionStorage.removeItem(name)
      } else {
        sessionStorage.setItem(name, JSON.stringify(!p.toJSON ? val : p.toJSON(val)))
      }
      return val
    },
  )
  return atom
}
