namespace $.$$ {
// ============================================================================

export interface $me_atom2_ec_cnf_intf {
  type?: string,
  skip_root_defaults?: boolean,
  base?: $me_atom2_ec_cnf_intf,

  prop?: $me_atom2_prop_intf,
  prop_default?: $me_atom2_prop_intf,

  control?: $me_atom2_control_intf,
  event?: $me_atom2_event_intf,
  dispatch?: (dispatch_name: string, dispatch_arg?: any) => boolean,

  init?: (ec: $me_atom2_ec) => void,//$me_fn<void>,
  fini?: (ec: $me_atom2_ec) => void,
  // fini?: $me_fn<void>,
}

export interface $me_atom2_ec_intf {
  [tail: string]: $me_atom2_prop_def<$me_atom2_ec_cnf_intf>,
}

export interface $me_atom2_ec_p_base_intf
  extends $me_atom2_entity_class_p_base_intf
  {
    cnf: $me_atom2_ec_cnf_intf,
    len_key?: number,
    key?: string | string[],
    keys?: string[],
    key_enum?: string[] | string[][],
  }

export interface $me_atom2_ec_p_intf
  extends $me_atom2_ec_p_base_intf
  {
    ent: $me_atom2_entity_enum,
  }

export class $me_atom2_ec extends $me_atom2_entity {
  static prop_default = <$me_atom2_prop_intf>{
    ...$me_atom2_prop_cascade(() => $me_align.left,
      '#align', ['#alignHor', '#alignVer']
    ),

    '#ofsHor': () => 0,
    '#ofsVer': () => 0,
    '#width': '<.#width',
    '#height': '<.#height',
  }


  get 'wait_for_child()'() {
    if (!this._wait_for_child) return null
    const result : {[name_entity: string]: any} = {}
    for (const name_entity of this._wait_for_child) {
      const entity = $me_atom2_entity.root().by_path_s(name_entity) as $me_atom2_ec
      // console.log(entity)
      result[name_entity] = typeof entity == 'string' ? entity : entity['wait_for_child()']
    }
    return result
  }

  // TODO: make two following private
  readonly cnf: $me_atom2_ec_cnf_intf
  // readonly bases: $me_atom2_ec_bases_type<$me_atom2_ec_cnf_intf>

  static _to_init = Array<$me_atom2_path>()
  private _init_did: boolean
  constructor (p: $me_atom2_ec_p_intf) {
    if (!p.key) {
      super(p)
    } else {
      let {ent, tail, parent} = p
      parent = parent.by_path(new $me_atom2_path({ent, tail}) )
      || new $me_atom2_entity({ent, tail, parent})
      const key = typeof p.key === 'string' ? [p.key as string] : p.key as string[]
      const i_last = p.keys.length - 2
      ent = $me_atom2_entity_enum.key
      for (let i = 0; i <= i_last; i++) {
        tail = key[i]
        parent = parent.by_path(new $me_atom2_path({ent, tail}))
        || new $me_atom2_entity({ent, tail, parent})
      }
      super({
        ent: $me_atom2_entity_enum.key,
        tail: key[i_last + 1],
        parent,
      })
    }
    this.cnf = p.cnf
    // this.bases = new Map<$me_fn<$me_atom2_ec_cnf_intf>, $me_atom2_ec_cnf_intf>()
  }
  protected _wait_for_child_do(name: string) {
    if (!this._wait_for_child) this._wait_for_child = new Set<string>()
    this._wait_for_child.add(name)
    // if (
    //   name.startsWith('/@app@workspace@search@panelResult@grid@row') &&
    //   name.length <= '/@app@workspace@search@panelResult@grid@row'.length + 4
    // ) console.warn(`%c${name}`, 'background: green; color: white')
  }
  // protected
  _wait_for_child_did(name: string = '') {
    if (name && this._wait_for_child) {
      // if (name.endsWith('@row')) console.warn(name)
      // if (
      //   name.startsWith('/@app@workspace@search@panelResult@grid@row') &&
      //   name.length <= '/@app@workspace@search@panelResult@grid@row'.length + 4
      // ) console.warn(`%c${name}`, 'background: red; color: white', this.name())
      this._wait_for_child.delete(name)
      if (!this._wait_for_child.size)
        this._wait_for_child = null
    }

    if (!this._wait_for_child) {
      // if (this.name() == '/@app@menu@ear') console.log('here')
      this._wait_for_child_did_helper()

      // this._isReady(true)
      $me_atom2_ec.almost_ready.add(this)
    }
  }
  static almost_ready = new Set<$me_atom2_ec>()
  // protected
  _isReady(val?: boolean) {
    const prop_isReady = this._entities.prop['#_isReady'] as $me_atom2<boolean>
    if (!prop_isReady) {
      console.error(this.name() + ' has no .#_isReady, but expected')
      $me_stop = true
      $me_throw('here')
      return
      return false
    }
    return prop_isReady.value(val)
  }
  protected _wait_for_child_did_helper() {
  }
  // private
  _wait_for_child: Set<string>

  private _active_did: boolean
  protected _on_active() {
    if (!this._active_did) {
      this._wait_for_child_did()
      this._active_did = true
    }
    $me_atom2_event_handlers = null
    if ($me_atom2_event_mousemove_last && !$me_atom2_event_mousemove_to_process) {
      $me_atom2_event_mousemove_to_process = $me_atom2_event_mousemove_last
      $me_atom2_async()
    }
  }
  destroy() {
    this.fini()
    super.destroy()
  }
  init() {
    if (this._init_did) return
    const prev = a.curr
    a.curr = this
    this._cnf_init(this.cnf)
    a.curr = prev
    this._init_did = true
  }
  fini() {
    if (!this._init_did) return
    const prev = a.curr
    a.curr = this
    this._cnf_fini(this.cnf)
    a.curr = prev
  }
  dispatch(dispatch_name: string, dispatch_arg?: any): boolean {
    const prev = a.curr
    a.curr = this
    const result = this._cnf_dispatch(this.cnf, dispatch_name, dispatch_arg)
    a.curr = prev
    return result
  }
  private _cnf_dispatch(cnf: $me_atom2_ec_cnf_intf, dispatch_name: string, dispatch_arg?: any): boolean {
    return !cnf ? false : (
      cnf.dispatch && cnf.dispatch(dispatch_name, dispatch_arg) ||
      this._cnf_dispatch(cnf.base, dispatch_name, dispatch_arg)
    )
  }
  private _cnf_init(cnf: $me_atom2_ec_cnf_intf) {
    if (!cnf) return
    this._cnf_init(cnf.base)
    if (cnf.init) cnf.init(this)
  }
  private _cnf_fini(cnf: $me_atom2_ec_cnf_intf) {
    if (!cnf) return
    if (cnf.fini) cnf.fini(this)
    this._cnf_fini(cnf.base)
  }
  protected cnf_item<Type>(name: string) {
    return this._cnf_item(name, this.cnf)
  }
  protected invalidateClientRect() {
    const prop_clientRect = this._entities.prop['#clientRect'] as $me_atom2<$me_rect_intf>
    if (!prop_clientRect) {
      console.error(this.name() + ' has no _entities.#clientRect, but expected')
      $me_stop = true
      $me_throw('here')
      return
    }
    if (prop_clientRect.state() === $me_atom2_state_enum.valid)
      prop_clientRect.set_state($me_atom2_state_enum.need_check)
      // prop_clientRect.set_state($me_atom2_state_enum.invalid)
    for (const ec_kind of ['elem', 'control']) {
      const entities_of_kind = this._entities[ec_kind]
      if (!entities_of_kind) continue
      for (const tail in entities_of_kind)
        this.invalidateClientRectHelper(entities_of_kind[tail])
    }
  }
  private invalidateClientRectHelper(entity: $me_atom2_entity) {
    // console.warn(this.name())
    if (!entity._entities) {
      console.error(entity.name() + ' has now _entities.key, but expected')
      $me_stop = true
      $me_throw('here')
      return
    }
    if (!entity._entities.key) {
      (entity as $me_atom2_ec).invalidateClientRect()
    } else {
      for (const tail in entity._entities.key)
        this.invalidateClientRectHelper(entity._entities.key[tail])
    }
  }
  protected _mk_controls(level = 0) {
    const prev = a.curr
    a.curr = this

    const cnf_control = this.cnf_items<$me_atom2_prop_compute_only_def<$me_atom2_control_cnf_intf>>('control')
    for (const tail in cnf_control) {
      const prop_def = cnf_control[tail]
      const fn_apply: $me_atom2_apply_fn<$me_atom2_ec_cnf_intf> = ({prev, val, len_key, key, keys, key_enum}) => {
        let path_abs = new $me_atom2_path({ent: $me_atom2_entity_enum.control, tail, path: this.path})
        let path = new $me_atom2_path({ent: $me_atom2_entity_enum.control, tail})
        if (key) {
          const ent = $me_atom2_entity_enum.key
          for (const tail of (typeof key === 'string' ? [key as string] : key as string[])) {
            path = new $me_atom2_path({ent, tail, path})
            path_abs = new $me_atom2_path({ent, tail, path: path_abs})
          }
        }
        // if (prev && val && prev.type === val.type) return
        if (prev && val && (prev === val || prev.type == val.type)) return
        const control = this.by_path(path) as $me_atom2_control
        if (control instanceof $me_atom2_control)
          control.destroy()
        if (val) {
          $me_atom2_control.to_def.push({
            cnf: val,
            parent: this,
            tail,
            len_key,
            key,
            keys,
            key_enum,
            level: level + 1,
          })
          $me_atom2_async()
        }
        if (!this._isReady()) {
          if (val) {
            this._wait_for_child_do(path_abs.toString())
          } else {
            this._wait_for_child_did(path_abs.toString())
          }
          if (key)
            this._wait_for_child_did(this.name() + '^' + tail)
        }
      }
      $me_atom2.to_def.push($me_atom2_prop_def_prepare<$me_atom2_control_cnf_intf>(
        prop_def,
        {
          parent: this,
          tail: '^' + tail,
          fn_apply,
        }
      ))
      this._wait_for_child_do(this.name() + '^' + tail)
      $me_atom2_async()
    }

    a.curr = prev
    return cnf_control
  }
  private _cnf_item<Value>(
    name: string,
    cnf: $me_atom2_ec_cnf_intf
  ): Value {
    return (
      !cnf ? null :
      cnf[name] !== void 0 ? cnf[name] as Value :
      // this._cnf_item<Value>(name, this.base(cnf.base))
      this._cnf_item<Value>(name, cnf.base)
    )
  }
  protected cnf_items<Type>(name: string) {
    const result = <{ [k: string]: Type }>{}
    this._cnf_items(name, this.cnf, result)
    return result
  }
  private _cnf_items<Type>(
    name: string,
    cnf: $me_atom2_ec_cnf_intf,
    result: { [k: string]: Type }
  ) {
    // if (this.name() === '/@app@game@shelf_canvas^checker[white][0]^circle_outer') console.log(cnf)
    if (!cnf) return
    if (cnf[name])
      for (const k in (cnf[name] as { [k: string]: Type }))
        if (result[k] === void 0 && cnf[name][k] != null)
          result[k] = cnf[name][k]
    // this._cnf_items<Type>(name, this.base(cnf.base), result)
    this._cnf_items<Type>(name, cnf.base, result)
  }
  // =======================================
  protected _prepare(
    name: string,
    ent: $me_atom2_entity_enum.control | $me_atom2_entity_enum.elem,
    dflt: $me_atom2_prop_intf,
    cnf?: $me_atom2_ec_cnf_intf,
    level?: number,
    skip_root_defaults?: boolean,
  ): $me_atom2_ec_prepare_ret_intf {
    if (level === void 0) {
      cnf = this.cnf
      level = 0
    }
    if (cnf) {
      if (level) {
        // if (!cnf.type) $me_throw(`${this.name()}: .base must have .type`)
      } else {
        // if (cnf.type) $me_throw('terminal control can not have .type, .type allowed only inside .base')
        if (cnf[name]) $me_throw(`terminal control can not have .${name}`)
      }
      skip_root_defaults = skip_root_defaults || cnf.skip_root_defaults
    }
    const result =
      level ?
        this._prepare_helper(name, ent, dflt, cnf, level, skip_root_defaults) :
        this._prepare(
          name,
          ent,
          dflt,
          cnf.base,
          level + 1,
          skip_root_defaults || cnf && cnf.base && cnf.base.skip_root_defaults,
        )
    if (
      cnf && cnf.skip_root_defaults === false &&
      cnf.base && cnf.base.skip_root_defaults === true
    ) {
      const {defaults, defaults_relative} = this._prepare_helper(name, ent, {}, null, 0, false)
      for (const prop in defaults)
        if (!result.defaults[prop])
          result.defaults[prop] = defaults[prop]
      for (const prop in defaults_relative)
        if (!result.defaults_relative[prop])
          result.defaults_relative[prop] = defaults_relative[prop]
      // console.warn(result, cnf)
    }
    return result
  }
  static _cnf_cache = new Map<$me_atom2_ec_cnf_intf, string>()
  private _prepare_helper(
    name: string,
    ent: $me_atom2_entity_enum.control | $me_atom2_entity_enum.elem,
    dflt: $me_atom2_prop_intf,
    cnf: $me_atom2_ec_cnf_intf,
    level: number,
    skip_root_defaults: boolean,
  ) {
    // const type = !cnf ? '$me' : cnf.type
    let type: string
    if (!cnf) {
      type = '$me'
      if (skip_root_defaults) {
        return <$me_atom2_ec_prepare_ret_intf>{
          defaults: { },
          defaults_relative: { },
        }
      }
    } else {
      if (!$me_atom2_ec._cnf_cache.has(cnf)) {
        const name = cnf.type || ($me_atom2_ec._cnf_cache.size + '')
        $me_atom2_ec._cnf_cache.set(cnf, name)
      }
      type = $me_atom2_ec._cnf_cache.get(cnf)
    }
    const cacheKey = $me_atom2_path_ent2prefix[ent] + type + ':' + name
    if (!$me_atom2_ec._prepare_cache[cacheKey]) {

      const result = cnf ?
        this._prepare(
          name,
          ent,
          dflt,
          cnf.base,
          level + 1,
          skip_root_defaults || cnf && cnf.base && cnf.base.skip_root_defaults,
        ) :
        <$me_atom2_ec_prepare_ret_intf>{
          defaults: {},
          defaults_relative: {},
        }

      const props = !cnf ? dflt : cnf[name] ? cnf[name] : <$me_atom2_prop_intf>{}
      const prop_default = <$me_atom2_prop_intf>{}
      const prop_default_relative = <$me_atom2_prop_intf>{}
      for (const prop in props) {
        const prop_def = props[prop]
        if (
          typeof prop_def === 'string' && !(prop_def as string).startsWith('/') ||
          prop_def && Array.isArray(prop_def.masters) && prop_def.masters.filter((s: string) => !s.startsWith('/')).length
        ) {
          prop_default_relative[prop] = prop_def
        } else {
          prop_default[prop] = prop_def
        }
      }
      {
        const src = result.defaults
        for (const prop in result.defaults)
          if (!prop_default[prop] && !prop_default_relative[prop])
            prop_default[prop] = src[prop]
      }
      {
        const src = result.defaults_relative
        for (const prop in src)
          if (!prop_default[prop] && !prop_default_relative[prop])
            prop_default_relative[prop] = src[prop]
      }
      {
        const result = <$me_atom2_ec_prepare_ret_intf>{
          defaults: {},
          defaults_relative: prop_default_relative,
        }
        const parent = new $me_atom2<$me_atom2_ec_prepare_ret_intf>({
          tail: cacheKey,
          fn_compute: () => result,
        })
        for (const tail in prop_default) {
          const atom = new $me_atom2(
            $me_atom2_prop_def_prepare(prop_default[tail], {tail, parent})
          )
          result.defaults[tail] = atom.name()
        }
        $me_atom2_ec._prepare_cache[cacheKey] = result
      }
    }
    const result = $me_atom2_ec._prepare_cache[cacheKey]
    return <$me_atom2_ec_prepare_ret_intf>{
      defaults: { ...result.defaults },
      defaults_relative: { ...result.defaults_relative },
    }
  }
  static _prepare_cache = <{[type_name: string]: $me_atom2_ec_prepare_ret_intf}>{}
}

// ============================================================================

export type $me_atom2_ec_bases_type<Value> = Map<$me_fn<Value>, Value>

export interface $me_atom2_ec_prepare_ret_intf {
  defaults: $me_atom2_prop_intf,
  defaults_relative: $me_atom2_prop_intf,
}

// ==========================================================================

export const $me_atom2_ec_body_cursor = $me_atom2_async_multi_origin<string, $me_atom2_path>({
  // default: 'default',
  name: 'cursor',
  default: null,
  raf_order: 100,
  flush: (cursor: string) => {
    if (cursor == null) cursor = 'default'
    if (document.body.style.cursor != cursor)
      document.body.style.cursor = cursor
  },
})// ============================================================================

// ============================================================================

export interface $me_atom2_control_cnf_intf
  extends $me_atom2_ec_cnf_intf
  {
    clean?: $me_atom2_control_clean_fn,
    render?: $me_atom2_control_render_fn,
    // render_post?: $me_atom2_control_render_fn,
  }

export type $me_atom2_control_render_fn =
  (p: $me_atom2_control_render_p_intf ) => void

export type $me_atom2_control_clean_fn =
  (p: $me_atom2_control_clean_p_intf ) => boolean

export interface $me_atom2_control_intf {
  [tail: string]: $me_atom2_prop_def<$me_atom2_control_cnf_intf>,
}

export interface $me_atom2_control_p_intf
  extends $me_atom2_ec_p_base_intf
  {
    cnf: $me_atom2_control_cnf_intf,
    level?: number,
  }

export enum $me_atom2_control_render_state_enum { cleaned, rendered }

export class $me_atom2_control extends $me_atom2_ec {
  // static prop_default: $me_atom2_prop_intf

  static pixelRatio() {
    return ($me_atom2_entity.root()._entities.prop['#pixelRatio'] as $me_atom2).value()
  }

  // ===================================

  static to_def = Array<$me_atom2_control_p_intf>()
  static _to_render = new Set<$me_atom2_path>()
  static _to_clean = new Set<$me_atom2_path>()

  // ===================================

  readonly level: number
  render_state: $me_atom2_control_render_state_enum
  constructor (p: $me_atom2_control_p_intf) {
    super({
      ...p,
      ent: $me_atom2_entity_enum.control,
    })
    this._mk_controls(this.level = p.level || 1)
    this._mk_props('<'.repeat(this.level))
  }
  protected _wait_for_child_did_helper() {
    const parent = this.parent(true) as $me_atom2_control
    if (parent) parent._wait_for_child_did(this.name())
    // if (parent) parent._add_child_to_reorder(this.path)
  }
  destroy() {
    if (this.level > 1) {
      $me_atom2_control._to_render.add(this.parent(true).path)
      $me_atom2_async()
    } else {
      $me_atom2_control.clean([this])
    }
    super.destroy()
  }
  static prop_non_render_default = <$me_atom2_prop_intf>{
  }
  private _mk_props(s_level: string) {
    const {defaults, defaults_relative} = this._prepare(
      'prop_default',
      $me_atom2_entity_enum.control,
      // $me_atom2_control.prop_default || {}
      // (this.cnf as any).node && (this.cnf as any).node.ns && (this.cnf as any).node.tag != 'svg' ? {} : (
        $me_atom2_control.prop_default || {}
        // )
      // $me_atom2_control.prop_default || {}
    )
    const prop_render = this.props([
      this.cnf_items<$me_atom2_prop_def>('prop'),
      defaults,
      defaults_relative,
      {
        '#hidden': () => false,
        '#zIndex': '<.#zIndex',
        // '#isHover': () => false,
      },
      {
        '#_isReady': () => false,
        '#isReady': $me_atom2_prop<boolean>(['<.#isReady', '.#_isReady'],
          $me_atom2_prop_compute_fn_and(),
          ({val}) => {
            if (val) $me_atom2_ec._to_init.push(this.path)
          },
        ),
        // '#_cursor': $me_atom2_prop<string>(['.#isHover', '.#cursor'],
        //   ({masters: [isHover, cursor]}) => !isHover ? 'default' : cursor,
        //   ({atom, val}) => {
        //     $me_atom2_ec_body_cursor({origin: atom.path, val: val})
        //   },
        // ),
        '#visible': $me_atom2_prop<boolean>(
          ['.#hidden', '<.#visible'],
          ({masters: [hidden, visible]}) => !hidden && visible,
          ({val}) => {
            if (val) {
              $me_atom2_control._to_render.add(this.path)
            } else
            // if (val) return
            if (this.level > 1) {
              $me_atom2_control._to_render.add(this.parent(true).path)
            } else {
              $me_atom2_control._to_clean.add(this.path)
            }
            $me_atom2_async()
          }
        ),
        '#ctxSize': s_level + '.#ctxSize',
        '#ctx': s_level + '.#ctx',
        '#left': $me_atom2_prop<number, [$me_align, number, number, number, number]>(
          ['.#alignHor', '<.#width', '.#width', '.#ofsHor'].concat(s_level.length < 2 ? [] : ['<.#left']),
          ({masters: [alignHor, width_parent, width, ofsHor, left_parent]}) => {
            left_parent = left_parent || 0
            const result =
              alignHor === $me_align.left ? left_parent + ofsHor :
              alignHor === $me_align.right ? left_parent + width_parent - width - ofsHor :
              left_parent + (width_parent - width) / 2 + ofsHor
            return result
          }
        ),
        '#top': $me_atom2_prop<number, [$me_align, number, number, number, number]>(
          ['.#alignVer', '<.#height', '.#height', '.#ofsVer'].concat(s_level.length < 2 ? [] : ['<.#top']),
          ({masters: [alignVer, height_parent, height, ofsVer, top_parent]}) => {
            top_parent = top_parent || 0
            const result =
              alignVer === $me_align.top ? top_parent + ofsVer :
              alignVer === $me_align.bottom ? top_parent + height_parent - ofsVer :
              top_parent + (height_parent - height) / 2 + ofsVer
              return result
          }
        ),
      },
    ], {
      def: ({tail, prop_def, prop_defined, p, idx, len}) => {
        if (tail == '#cursor') {
          console.error(`${this.name()}: prop "#cursor" must be defined in "prop_non_render" section`)
          return null
        }
      },
      dup: ({tail, prop_defined, idx, len}) => {
        if (idx === len - 1)
          $me_throw(`${this.name()}: .${tail} reserved for internal use` + (tail !== '#visible' ? '' : ', use .#hidden instead'))
      }
    })
  {
    const {defaults, defaults_relative} = this._prepare(
      'prop_non_render_default',
      $me_atom2_entity_enum.control,
      $me_atom2_control.prop_non_render_default || {}
    )
    // console.log(defaults, defaults_relative)
    const prop_non_render = this.props([
      this.cnf_items<$me_atom2_prop_def>('prop_non_render'),
      defaults,
      defaults_relative,
      {
        '#isHover': () => false,
      },
      {
        '#_cursor': $me_atom2_prop<string>(['.#isHover', '.#cursor'],
          ({masters: [isHover, cursor]}) => !isHover ? null : cursor,
          ({atom, val}) => {
            $me_atom2_ec_body_cursor({origin: atom.path, val: val})
          },
        ),
      },
    ], {
      def: ({tail, prop_def, prop_defined, p, idx, len}) => {
        if (idx === len - 2 && tail == '#isHover') {
          if (prop_defined['#cursor'] === void 0) return null
        } else if (idx === len - 1 && tail == '#_cursor') {
          if (prop_defined['#cursor'] === void 0) return null
        }
      },
      dup: ({tail, prop_defined, idx, len}) => {
        if (idx === len - 1)
          $me_throw(`${this.name()}: .${tail} reserved for internal use` + (tail !== '#visible' ? '' : ', use .#hidden instead'))
      }
    })
  }

    for (const prop of ['#width', '#height', '#alignHor', '#alignVer', '#ofsHor', '#ofsVer'])
      if (prop_render[prop] === void 0)
        $me_throw(`${this.name()}: requires .${prop} to be defined`)

    this.props({
      '#render': $me_atom2_prop<any>(
        (Object as any).keys(prop_render).map((s: string) => '.' + s),//TODO: concat('.isFocused'),
        ({masters}) => !(this.active() && a('.#visible') && a('.#isReady')) ? null : masters,
        ({val, prev, atom}) => {
          if (!val) return
          // console.warn('to_render', this.path.toString(), atom.masters, val, prev)
          $me_atom2_control._to_render.add(this.path)
          $me_atom2_async()
        },
      ),
      // non_render
      '#offsetRect': () => $me_rect(),
      '#clientRect': $me_atom2_prop<$me_rect_intf>(
        [s_level + '.#clientRect', '.#offsetRect'],
        ({masters: [clientRect, offsetRect]}) => {
          return <$me_rect_intf>{
            left: clientRect.left + offsetRect.left,
            top: clientRect.top + offsetRect.top,
            right: clientRect.left + offsetRect.right,
            bottom: clientRect.top + offsetRect.bottom,
          }
        },
        prop_render['#isHover'] === void 0 ? null :
        ({val, atom}) => {
          if ($me_atom2_event_mousemove_last && !$me_atom2_event_mousemove_to_process) {
            $me_atom2_event_mousemove_to_process = $me_atom2_event_mousemove_last
            $me_atom2_async()
          }
        }
      ),
    })
  }
  // ===========================================

  static font_prepare(ctx: CanvasRenderingContext2D, pixelRatio: number, prefix = '') {
    const ctxFontSize = Math.round($me_atom2_control.font_size(pixelRatio, prefix))
    ctx.font = $me_atom2_control._fontWeight(prefix) + ' ' + ctxFontSize + 'px ' + $me_atom2_control._fontFamily(prefix)
    ctx.textAlign = 'left'
    ctx.textBaseline = 'bottom'
    return ctxFontSize
  }

  static font_size(pixelRatio: number, prefix = '') {
    const prop = a.curr.by_path_s($me_atom2_control._fontProp('fontSize', prefix)) as $me_atom2<number>
    if (typeof prop === 'string') $me_throw(prop)
    const result = prop.value() * pixelRatio
    return result
  }

  private static _fontProp(prop: string, prefix: string) {
    const result = '.' + (!prefix ? prop : prefix + prop.slice(0, 1).toUpperCase() + prop.slice(1))
    return result
  }

  private static _fontFamily(prefix = '') {
    const prop = a.curr.by_path_s($me_atom2_control._fontProp('fontFamily', prefix)) as $me_atom2<string>
    return !prefix || typeof prop !== 'string' ? prop.value() : a('.fontFamily')
  }

  private static _fontWeight(prefix = '') {
    const prop = a.curr.by_path_s($me_atom2_control._fontProp('fontWeight', prefix)) as $me_atom2<string>
    return !prefix || typeof prop !== 'string' ? prop.value() : a('.fontWeight')
  }

  // ===========================================

  static clean(controls: $me_atom2_control[], force = false ) {
    let count = 0
    // console.log('clean', controls.map(control => control.name()))
    for (let control of controls) {
      if (control.render_state !== $me_atom2_control_render_state_enum.rendered) continue
      count++

      const ctxRect = control._ctxRect()
      const p = <$me_atom2_control_clean_p_intf>{
        ctx: (control._entities.prop['#ctx'] as $me_atom2<CanvasRenderingContext2D>).value(),
        ctxRect,
      }
      if (p.ctx) {
        const prev = a.curr
        a.curr = control
        // console.warn('%cclean', 'background: red; color: white', control.name())
        if (!control._clean_helper(p, control.cnf as $me_atom2_control_cnf_intf))
          p.ctx.clearRect(
            p.ctxRect.left,
            p.ctxRect.top,
            p.ctxRect.right - p.ctxRect.left + 1, // +1 due to sierp/control/
            p.ctxRect.bottom - p.ctxRect.top + 1, // +1 due to sierp/control/
          )
        a.curr = prev
      }
      control.render_state = $me_atom2_control_render_state_enum.cleaned

      let controls: $me_atom2_control[]
      if ($me_atom2_control._fill_controls_cache.has(control)) {
        controls = $me_atom2_control._fill_controls_cache.get(control)
      } else {
        controls = []
        const entities_control = control._entities.control
        $me_atom2_control._fill_controls(controls, control._entities.control)
        $me_atom2_control._fill_controls_cache.set(control, controls)
      }
      if (controls.length)
        $me_atom2_control.clean($me_atom2_control.zIndex_sort(controls), force)
    }
    return count
  }
  private _ctxRect() {
    const prop = this._entities.prop
    const prop_offsetRect = prop['#offsetRect'] as $me_atom2<$me_rect_intf>
    const offsetRect = prop_offsetRect.value()
    const pixelRatio = $me_atom2_control.pixelRatio()
    return <$me_rect_intf>{
      left: offsetRect.left * pixelRatio,
      top: offsetRect.top * pixelRatio,
      right: offsetRect.right * pixelRatio,
      bottom: offsetRect.bottom * pixelRatio,
    }
  }
  private _clean_helper(
    p: $me_atom2_control_clean_p_intf,
    cnf: $me_atom2_control_cnf_intf,
  ): boolean {
    let result = false
    if (cnf) {
      if (cnf.clean) result = cnf.clean(p)
      // result = this._clean_helper(p, this.base(cnf.base)) || result
      result = this._clean_helper(p, cnf.base) || result
    }
    return result
  }

  static fill_controls_cache_clear() {
    $me_atom2_control._fill_controls_cache.clear()
  }
  private static _fill_controls_cache = new Map<$me_atom2_control, $me_atom2_control[]>()
  private static _fill_controls(
    controls: $me_atom2_control[],
    entities_of_type: {[entity_name: string]: $me_atom2_entity}
  ) {
    for (const tail in entities_of_type) {
      const entity = entities_of_type[tail]
      if (!entity._entities.key) {
        controls.push(entity as $me_atom2_control)
      } else {
        $me_atom2_control._fill_controls(controls, entity._entities.key)
      }
    }
  }

  // ===========================================

  static render(controls: $me_atom2_control[], pixelRatio?: number) {
    if (pixelRatio === void 0)
      pixelRatio = $me_atom2_control.pixelRatio()
    let count = 0
    // console.log('render', controls.map(control => control.name()))
    for (let control of controls) {
      if (control.render_state === $me_atom2_control_render_state_enum.rendered) {
        // console.error('render skip for ' + control.name())
        continue
      }
      count++

      const prop = control._entities.prop
      const prop_offsetRect = prop['#offsetRect'] as $me_atom2<$me_rect_intf>
      const prop_width = prop['#width'] as $me_atom2
      const prop_heght = prop['#height'] as $me_atom2
      const prop_left = prop['#left'] as $me_atom2
      const prop_top = prop['#top'] as $me_atom2

      const ctxWidth = Math.round(prop_width.value() * pixelRatio)
      const ctxHeight = Math.round(prop_heght.value() * pixelRatio)
      const left = Math.round(prop_left.value() * pixelRatio)
      const top = Math.round(prop_top.value() * pixelRatio)
      const right = left + ctxWidth
      const bottom = top + ctxHeight

      const ctxRect = <$me_rect_intf>{left, top, right, bottom}

      prop_offsetRect.value(<$me_rect_intf>{
        left: left / pixelRatio,
        top: top / pixelRatio,
        right: right / pixelRatio,
        bottom: bottom / pixelRatio,
      })
      const p = <$me_atom2_control_render_p_intf>{
        self: control,
        ctx: (control._entities.prop['#ctx'] as $me_atom2<CanvasRenderingContext2D>).value(),
        ctxSize: (control._entities.prop['#ctxSize'] as $me_atom2<$me_size_intf>).value(),
        pixelRatio,
        ctxRect,
        ctxWidth,
        ctxHeight,
      }
      {
        const prev = a.curr
        a.curr = control
        control._render_helper(p, control.cnf)
        // console.warn('%crender', 'background: green; color: white', control.name())
        if (typeof a.curr.by_path_s('.#border') !== 'string') {
          p.ctx.strokeStyle = a<string>('.#border')
          p.ctx.strokeRect(p.ctxRect.left, p.ctxRect.top, p.ctxRect.right - p.ctxRect.left, p.ctxRect.bottom - p.ctxRect.top)
        }
        a.curr = prev
      }

      let controls: $me_atom2_control[]
      if ($me_atom2_control._fill_controls_cache.has(control)) {
        controls = $me_atom2_control._fill_controls_cache.get(control)
      } else {
        controls = []
        const entities_control = control._entities.control
        $me_atom2_control._fill_controls(controls, control._entities.control)
        $me_atom2_control._fill_controls_cache.set(control, controls)
      }
      if (controls.length)
        $me_atom2_control.render(
          $me_atom2_control.zIndex_sort(controls),
          pixelRatio,
        )

      // {
      //   const prev = a.curr
      //   a.curr = control
      //   control_render_post_helper(p, control.cnf, control.bases)
      //   a.curr = prev
      // }

      control.render_state = $me_atom2_control_render_state_enum.rendered
    }
    return count
  }

  private _render_helper(p: $me_atom2_control_render_p_intf, cnf: $me_atom2_control_cnf_intf) {
    if (!cnf) return
    // this._render_helper(p, this.base(cnf.base))
    this._render_helper(p, cnf.base)
    if (cnf.render) cnf.render(p)
  }

  // ===========================================

  static zIndex_sort(ss: Set<$me_atom2_path> | Array<$me_atom2_control>, to_render = false) {
    return (
      Array.isArray(ss) ? ss :
      [...ss].map(path => $me_atom2_entity.root().by_path(path) as $me_atom2_control)
    )
    .filter(control => control && control.active &&
      (() => {
        if (!control._entities.prop)
          $me_throw(control.name());
        return true
      })() &&
      (!to_render || (control._entities.prop['#visible'] as $me_atom2<boolean>).value()))
    // .filter(name_control => $me_atom_entities.has(name_control))
    // .sort((sa: string, sb: string) => {
    //   const sav = $me_atom.get(sa + '.zIndex').value()
    //   const sbv = $me_atom.get(sb + '.zIndex').value()
    //   return to_render ? sav - sbv : sbv - sav
    // })
    // .map(name_control => $me_atom_entity_get(name_control))
  }
}

// ============================================================================

export interface $me_atom2_control_clean_p_intf {
  ctx: CanvasRenderingContext2D,
  ctxRect: $me_rect_intf,
}

export interface $me_atom2_control_render_p_intf {
  self: $me_atom2_control,
  ctx: CanvasRenderingContext2D,
  pixelRatio: number,
  ctxSize: $me_size_intf,
  ctxRect: $me_rect_intf,
  ctxWidth: number,
  ctxHeight: number,
}

// ============================================================================
}