namespace $.$$ {
// ============================================================================

// https://github.com/epoberezkin/fast-deep-$me_equal/blob/master/index.js
let isArray = Array.isArray
let keyList = Object.keys
let hasProp = Object.prototype.hasOwnProperty

export type $me_equal_type<Value> = (a: Value, b: Value) => boolean
export function $me_equal(a: any, b: any) : boolean {
  if (a === b) return true
  if (a && b && typeof a == 'object' && typeof b == 'object') {

    const arrA = isArray(a) , arrB = isArray(b)
    if (arrA != arrB) return false
    if (arrA && arrB) {
      const length = a.length
      if (length != b.length) return false
      for (let i = length; i-- !== 0;)
        if (!$me_equal(a[i], b[i])) {
          // if (length == 27) console.warn(i, a[i], b[i])
          return false
        }
      return true
    }

    const setA = a instanceof Set, setB = b instanceof Set
    if (setA != setB) return false
    if (setA && setB) {
      if (a.size != b.size) return false
      let iter = a.keys()
      let next = iter.next()
      while (!next.done) {
        if (!b.has(next.value)) return false
        next = iter.next()
      }
      iter = b.keys()
      next = iter.next()
      while (!next.done) {
        if (!a.has(next.value)) return false
        next = iter.next()
      }
      return true
    }

    const mapA = a instanceof Map, mapB = b instanceof Map
    if (mapA != mapB) return false
    if (mapA && mapB) return $me_equal([...a], [...b])

    const dateA = a instanceof Date, dateB = b instanceof Date
    if (dateA != dateB) return false
    if (dateA && dateB) return a.getTime() == b.getTime()

    const regexpA = a instanceof RegExp, regexpB = b instanceof RegExp
    if (regexpA != regexpB) return false
    if (regexpA && regexpB) return a.toString() == b.toString()

    // const canvasA = a instanceof CanvasRenderingContext2D
    // const canvasB = b instanceof CanvasRenderingContext2D
    // if (canvasA != canvasB) return false
    // if (canvasA && canvasB) return (a as CanvasRenderingContext2D).canvas == (b as CanvasRenderingContext2D).canvas

    const keys = keyList(a)
    const length = keys.length
    if (length !== keyList(b).length) return false
    for (let i = length; i-- !== 0;)
      if (!hasProp.call(b, keys[i])) return false
    for (let i = length; i-- !== 0;) {
      const key = keys[i]
      if (!$me_equal(a[key], b[key])) return false
    }
    return true
  }
  return a!==a && b!==b
}

// ==========================================================================
}
