export interface $me_size_intf {
  width: number,
  height: number,
}

export interface $me_point_intf {
  x: number,
  y: number,
}

export interface $me_vector_intf {
  from: $me_point_intf,
  to: $me_point_intf,
}

export function $me_vector_transform(
  v: $me_vector_intf,
  dist: number,
  from?: $me_point_intf,
): $me_vector_intf {
  const dx = v.to.x - v.from.x
  const dy = v.to.y - v.from.y
  const len = Math.hypot(dx, dy)
  if (!from) from = v.from
  const result = {
    from,
    to: {
      x: dx / len * dist + from.x,
      y: dy / len * dist + from.y,
    },
  }
  return result
}

export function $me_vector_revert(
  v: $me_vector_intf,
) {
  const result = {
    from: v.to,
    to: v.from,
  }
  return result
}


export interface $me_rect_intf {
  top: number,
  bottom: number,
  left: number,
  right: number,
}

export const $me_rect = () => ({left: 0, top: 0, right: 0, bottom: 0})

export const $me_rect_width = (rect: $me_rect_intf) =>
  rect.right - rect.left

export const $me_rect_height = (rect: $me_rect_intf) =>
  rect.bottom - rect.top

// export const $me_rect = () => ({left: 0, top: 0, right: 0, bottom: 0})

export interface $me_pos_intf {
  top?: number,
  bottom?: number,
  left?: number,
  right?: number,
}

export const $me_pos = () => ({left: 0, top: 0})

export const $me_point_in_rect = (x: number, y: number, rect: $me_rect_intf, tolerance = 0) =>
  rect.left - tolerance < x && x < rect.right + tolerance &&
  rect.top - tolerance < y && y < rect.bottom + tolerance

export const $me_dist_to_rect = (x: number, y: number, rect: $me_rect_intf) =>
  $me_point_in_rect(x, y, rect) ? 0 :
  Math.max(
    Math.min(
      Math.abs(rect.left - x),
      Math.abs(rect.right - x),
    ),
    Math.min(
      Math.abs(rect.top - y),
      Math.abs(rect.bottom - y),
    )
  )


export function $me_point_in_rect_offset(
  x: number,
  y: number,
  offsetRect: $me_rect_intf,
  clientRect: $me_rect_intf
) {
  x -= clientRect.left
  y -= clientRect.top
  const result =
    offsetRect && clientRect &&
    offsetRect.left < x && x < offsetRect.right &&
    offsetRect.top < y && y < offsetRect.bottom &&
  true
  return result
}

