export interface $me_options_caption_intf {
  text: string,
  width?: number,
}

export type $me_options_caption = string | $me_options_caption_intf

export type $me_options_caption_fn =
  (p: {
    isSelected: boolean,
    val: string|Set<string>|Map<string, null>,
  }) => $me_options_caption

export interface $me_option_intf {
  caption?: $me_options_caption | $me_options_caption_fn
}

export interface $me_options {
  [id: string]: $me_option_intf
}

export function $me_option_caption(
  id: string,
  options: $me_options,
  val?: string | Set<string> | Map<string, null>,
): $me_options_caption {
  const isSelected =
    typeof val == 'string' ? val == id :
    val instanceof Set ? (val as Set<string>).has(id) :
    val instanceof Map ? (val as Map<string, null>).has(id) :
    false
  const caption : $me_options_caption =
    typeof options[id].caption == 'function' ?
    (options[id].caption as $me_options_caption_fn)({val, isSelected}) :
    options[id].caption as $me_options_caption
  return caption
}

export function $me_option_caption_text(
  id: string,
  options: $me_options,
  val?: string | Set<string> | Map<string, null>,
): string {
  const caption = $me_option_caption(id, options, val)
  const result =
    typeof caption == 'string' ? caption as string :
    !caption || typeof (caption as $me_options_caption_intf).text != 'string' ? id :
    (caption as $me_options_caption_intf).text
  return result
}

