export const $me_enum_names = (val: Object) => {
  if (_enum_names_cache.has(val)) return _enum_names_cache.get(val)
  const result = Object.keys(val).filter(key => typeof val[key] == 'number') as string[]
  _enum_names_cache.set(val, result)
  return result
}
const _enum_names_cache = new Map<Object, string[]>()

export const $me_enum_values = (val: Object) => {
  if (_enum_values_cache.has(val)) return _enum_values_cache.get(val)
  const result = Object.keys(val).reduce((result, key) => {
    const n = +key
    if (Number.isFinite(n)) result.push(n)
    return result
  }, Array<number>())
  _enum_values_cache.set(val, result)
  return result
}
const _enum_values_cache = new Map<Object, number[]>()

export const $me_enum_pairs = (val: Object) => {
  if (_enum_pairs_cache.has(val)) return _enum_pairs_cache.get(val)
  const result = $me_enum_values(val).map(value => [ value, val[value] ]) as [number, string][]
  // if (_enum_values_cache.has(val)) return _enum_values_cache.get(val)
  // const result = Object.keys(val).filter(key => typeof val[key] == 'string')
  _enum_pairs_cache.set(val, result)
  return result
}
const _enum_pairs_cache = new Map<Object, [number, string][]>()

