export let $me_atom2_event_mousemove_last: MouseEvent
export let $me_atom2_event_wheel_to_process: $me_atom2_event_wheel_intf
// export let $me_atom2_event_wheel_touch_to_process: $me_atom2_event_wheel_touch_intf
export let $me_atom2_event_handlers : Map<string, $me_atom2_event_handlers_queue_type>
export interface $me_atom2_event_tap_target_intf {
  ec: $me_atom2_ec,
  dist: number,
  event: TouchEvent,
}

export let $me_atom2_event_click : Set<$me_atom2_path>
let startClick: MouseEvent
export let $me_atom2_event_tap: Map<$me_atom2_path, number>
let startTap: TouchEvent
let hoverCurr: Set<$me_atom2_path>
let hoverPrev: Set<$me_atom2_path>

function _event_add<Event>(ec: $me_atom2_ec, name_event: string, events?: $me_atom2_event_intf) {
  let name_atom: string, fn: $me_atom2_event_fn<any>
  if (events) {
    const event_def = events[name_event]
    fn = typeof event_def === 'function' ?
      (event_def as $me_atom2_event_fn<any>) :
      (event_def as $me_atom2_event_def_intf<any>).fn
  }
  let s: string
  if (!ec._entities) $me_throw(ec.name(), ec.active())
  if (!ec._entities.prop) $me_throw(ec.name(), ec.active(), ec._entities)
  if (!ec._entities.prop['#zIndex']) $me_throw(ec.name(), ec.active(), ec._entities.prop)
  const zIndex = (ec._entities.prop['#zIndex'] as $me_atom2).value()
  if (name_event === 'hover') {
    _do_event_add<MouseEvent>(ec, 'mousemove', zIndex, {name: 'hover'})
  } else if (name_event === 'tap' || name_event === 'clickOrTap' && isTouch()) {
    _do_event_add<TouchEvent>(ec, 'touchstart', zIndex, {name: 'tapBegin'})
    _do_event_add<TouchEvent>(ec, 'touchmove', zIndex, {name: 'tapTrack'})
    _do_event_add<TouchEvent>(ec, 'touchend', zIndex, {name: 'tapDid', fn})
  } else if (name_event === 'tapOutside' || name_event === 'clickOrTapOutside' && isTouch()) {
    _do_event_add<TouchEvent>(ec, 'touchstart', zIndex, {name: 'tapOutside', fn})
    // _do_event_add<TouchEvent>(ec, 'touchmove', zIndex, {name: 'tapOutsideTrack'})
    // _do_event_add<TouchEvent>(ec, 'touchend', zIndex, {name: 'tapOutsideDid', fn})
  } else if (name_event === 'click' || name_event === 'clickOrTap' && !isTouch()) {
    _do_event_add<MouseEvent>(ec, 'mousedown', zIndex, {name: 'clickBegin'})
    _do_event_add<MouseEvent>(ec, 'mouseup', zIndex, {name: 'clickDid', fn})
  } else if (name_event === 'clickOutside' || name_event === 'clickOrTapOutside') {
    _do_event_add<MouseEvent>(ec, 'mousedown', zIndex, {name: 'clickOutside', fn})
    // _do_event_add<MouseEvent>(ec, 'mouseup', zIndex, {name: 'clickOutsideDid', fn})
    // $me_throw('TODO')
    // _do_event_add<MouseEvent>(ec, 'mousedown', zIndex, p =>
    //   !p.isInRect(p.event.clientX, p.event.clientY) && fn(p)
    // )
  } else
    _do_event_add<any>(ec, name_event, zIndex, fn)
}

function _do_event_add<Event>(
  ec: $me_atom2_ec,
  name_event: string,
  zIndex: number,
  fn: $me_atom2_event_fn<Event> | special_event_handler
) {
  if (!$me_atom2_event_handlers.has(name_event))
    $me_atom2_event_handlers.set(name_event, [] as $me_atom2_event_handlers_queue_type)
  const queue = $me_atom2_event_handlers.get(name_event)
  let i = 0
  while (i < queue.length && queue[i].zIndex > zIndex) i++
  if (i == queue.length || queue[i].zIndex != zIndex)
    queue.splice(i, 0, {
      zIndex,
      handlers: new Map<$me_atom2_path, $me_atom2_event_fn<any>[]>(),
    })
  const handlers = queue[i].handlers
  if (!handlers.has(ec.path)) handlers.set(ec.path, [] as $me_atom2_event_fn<any>[])
  handlers.get(ec.path).push(fn)
}

function _events_add_helper(
  ec: $me_atom2_ec,
  cnf: $me_atom2_ec_cnf_intf,
) {
  if (!cnf) return
  if (cnf.event)
    for (const name_event of event_names)
      if (cnf.event[name_event]) _event_add(ec, name_event, cnf.event)
  if (cnf.base)
    _events_add_helper(ec, cnf.base)
}

function _events_add(ec: $me_atom2_ec) {
  if (
    ec._entities &&
    ec._entities.prop &&
    ec._entities.prop['#isHover']
    && !(ec._entities.prop['#isHover'] as $me_atom2).masters
  ) _event_add(ec, 'hover')

  _events_add_helper(ec, ec.cnf)
}

function _events_add_recursive(entity: $me_atom2_entity) {
  const entities = entity._entities
  if (entities) {
    for (const ent of [$me_atom2_entity_enum.key, $me_atom2_entity_enum.elem, $me_atom2_entity_enum.control]) {
      const entities_of_type = entities[$me_atom2_entity_enum[ent]]
      if (!entities_of_type) continue
      for (const tail in entities_of_type) {
        const ec = entities_of_type[tail] as $me_atom2_ec
        if (!ec.active()) continue
        _events_add(ec)
        _events_add_recursive(ec)
      }
    }
  }
}

export function $me_atom2_event_process(name_event: string, event: any) {
  if (!event) return
  if (!$me_atom2_event_handlers) {
    $me_atom2_event_handlers = new Map()
    _events_add_recursive($me_atom2_entity.root())
    // console.log($me_atom2_event_handlers)
  }
  if (!$me_atom2_event_handlers.has(name_event)) return
  const queue = $me_atom2_event_handlers.get(name_event)
  let done_event = false
  let ec_proceed: $me_atom2_ec
  QUEUE: for (const item of queue) {
    for (const [path, fn_array] of item.handlers) {
      const ec = $me_atom2_entity.root().by_path<$me_atom2_ec>(path)
      if (!ec) continue
      if (!(ec._entities.prop['#visible'] as $me_atom2<boolean>).value()) continue

      const clientRect = (ec._entities.prop['#clientRect'] as $me_atom2<$me_rect_intf>).value()
      if (!clientRect) continue

      const isInRect = (clientX: number, clientY: number) =>
        $me_point_in_rect(clientX, clientY, clientRect)
      const distToRect = (clientX: number, clientY: number) =>
        $me_dist_to_rect(clientX, clientY, clientRect)

      let done_ec = false
      let done_ec_special = false

      const prev = a.curr
      a.curr = ec
      let clickTolerance: number
      let touchTolerance: number
      let tapTolerance: number
      for (const item of fn_array)
        if (typeof item == 'function') {
          const fn = item as $me_atom2_event_fn<any>
          if (done_ec = fn({event, isInRect, distToRect}))
            break
        } else {
          const name = (item as special_event_handler).name
          if (name == 'hover') {
            if (isInRect(event.clientX, event.clientY)) {
              (ec._entities.prop['#isHover'] as $me_atom2<boolean>).value(true)
              if (!hoverCurr) hoverCurr = new Set<$me_atom2_path>()
              hoverCurr.add(path)
              // done_ec_special = true
            }
          } else if (name == 'clickOutside') {
            done_ec_special =
              !isInRect(event.clientX, event.clientY) &&
              (item as special_event_handler).fn(
                <$me_atom2_event_p_intf<MouseEvent>>{
                  isInRect,
                  distToRect,
                  event,
                }
              )
          } else if (name == 'clickBegin') {
            if (isInRect(event.clientX, event.clientY)) {
              if (!$me_atom2_event_click) $me_atom2_event_click = new Set<$me_atom2_path>()
              $me_atom2_event_click.add(path)
              startClick = event
              // done_ec_special = true
            }
          } else if (name == 'clickDid') {
            if ($me_atom2_event_click && $me_atom2_event_click.has(path)) {
              // const start = click.get(path)
              if (clickTolerance === void 0)
                clickTolerance = a('/.#clickTolerance')
              done_ec_special =
                Math.abs(startClick.clientX - event.clientX) <= clickTolerance &&
                Math.abs(startClick.clientY - event.clientY) <= clickTolerance &&
                (item as special_event_handler).fn(
                  <$me_atom2_event_p_intf<$me_atom2_event_click_intf>>{
                    isInRect,
                    distToRect,
                    event: {start: startClick, end: event}
                  }
                )
            }
          } else if (name == 'tapOutside') {
            if (touchTolerance === void 0)
              touchTolerance = a('/.#touchTolerance')
            const dist = distToRect(event.touches[0].clientX, event.touches[0].clientY)
            done_ec_special =
              (dist > touchTolerance) &&
              (item as special_event_handler).fn(
                <$me_atom2_event_p_intf<TouchEvent>>{
                  isInRect,
                  distToRect,
                  event,
                }
              )
          } else if (name == 'tapBegin') {
            if (touchTolerance === void 0)
              touchTolerance = a('/.#touchTolerance')
            const dist = distToRect(event.touches[0].clientX, event.touches[0].clientY)
            if (dist <= touchTolerance) {
              if (!$me_atom2_event_tap) {
                $me_atom2_event_tap = new Map<$me_atom2_path, number>()
              } else {
                const dist_eta = dist
                for (const [path, dist] of $me_atom2_event_tap)
                  if (dist > dist_eta) $me_atom2_event_tap.delete(path)
              }
              $me_atom2_event_tap.set(ec.path, dist)
              startTap = event
              // done_ec_special = true
            }
          } else if (name == 'tapTrack') {
            if ($me_atom2_event_tap && $me_atom2_event_tap.size) {
              const event_curr = event
              if (tapTolerance == void 0)
                tapTolerance = a('/.#tapTolerance')
              if (
                Math.abs(event.touches[0].clientX - startTap.touches[0].clientX) > tapTolerance ||
                Math.abs(event.touches[0].clientY - startTap.touches[0].clientY) > tapTolerance
              ) $me_atom2_event_tap.clear()
            }
          } else if (name == 'tapDid') {
            if ($me_atom2_event_tap && $me_atom2_event_tap.has(path)) {
              const fn = (item as special_event_handler).fn
              if (fn(<$me_atom2_event_p_intf<$me_atom2_event_tap_intf>>{
                isInRect,
                distToRect,
                event: {start: startTap, end: event},
              })) {
                const app = a.get('/@app') as $me_atom2_ec
                app.dispatch('tapEffect', {ec, event: startTap})
                done_ec_special = true
              }
            }
          } else $me_throw(name)
        }
      a.curr = prev
      // if (name_event == 'mousedown' && (done_ec || done_ec_special)) console.warn(ec.name())

      done_event = done_event || done_ec || done_ec_special
      // if (name_event == 'mouseup') {
      //   console.log(path.toString(), {done_event, done_ec, done_ec_special}, [...click])
      // }
      if (done_event) {
        ec_proceed = ec
        break QUEUE
      }
    }
    // if (done_event) break
  }
  if (name_event == 'mousemove') {
    if (hoverCurr) {
      if (hoverPrev && hoverPrev.size) {
        for (const path of hoverPrev) {
          if (hoverCurr.has(path)) continue
          const ec = $me_atom2_entity.root().by_path(path) as $me_atom2_ec
          if (!(ec instanceof $me_atom2_ec)) continue
          (ec._entities.prop['#isHover'] as $me_atom2<boolean>).value(false)
        }
        hoverPrev.clear()
      }
      if (!hoverPrev) hoverPrev = new Set<$me_atom2_path>()
      for (const path of hoverCurr)
        hoverPrev.add(path)
      hoverCurr.clear()
    }
  } else if (name_event == 'mouseup') {
    $me_atom2_event_click && $me_atom2_event_click.clear()
  } else if (name_event == 'touchend') {
    $me_atom2_event_tap && $me_atom2_event_tap.clear()
  }
  if (
    event.preventDefault &&
    (ec_proceed instanceof $me_atom2_elem) &&
    (ec_proceed as $me_atom2_elem).node.tagName != 'INPUT'
  ) {
    event.preventDefault()
  // } else {
  //   console.warn(event, ec_proceed)
  }
}

