export enum $me_atom2_entity_enum {root, key, prop, control, elem}

export interface $me_atom2_entity_class_p_base_intf {
  tail?: string,
  parent?: $me_atom2_entity,
}

export interface $me_atom2_entity_class_p_intf
  extends $me_atom2_entity_class_p_base_intf
  {
    ent: $me_atom2_entity_enum,
  }

export let $me_atom2_entity_saved = 0
export let $me_atom2_entity_saved_stat = <{[tail: string]: number}>{}

export class $me_atom2_entity {
  static root(): $me_atom2_entity {
    if (!$me_atom2_entity._root)
      $me_atom2_entity._root = new $me_atom2_entity({ent: $me_atom2_entity_enum.root})
    return $me_atom2_entity._root
  }
  static by_path_s<Entity extends $me_atom2_entity=$me_atom2_entity>(s: string) {
    return $me_atom2_entity.root().by_path_s(s) as Entity
  }
  static by_path<Entity extends $me_atom2_entity=$me_atom2_entity>(path: $me_atom2_path) {
    return $me_atom2_entity.root().by_path(path) as Entity
  }
  private static _root: $me_atom2_entity

  // ===========================

  readonly path: $me_atom2_path
  constructor (p: $me_atom2_entity_class_p_intf) {
    const pp = <$me_atom2_path_class_p_intf>{ ent: p.ent }
    const isRoot = p.ent === $me_atom2_entity_enum.root
    if (isRoot) {
      this._active = true
    } else {
      this._active = true
      pp.tail = p.tail
      pp.path = p.parent && p.parent.path ||  $me_atom2_entity.root().path
    }
    this.path = new $me_atom2_path(pp)
    if (!isRoot) {
      const parent = this.parent()
      const enitites = parent._entities || (parent._entities = {})

      const ent_name = $me_atom2_entity_enum[p.ent]
      const enitites_of_type = enitites[ent_name] || (enitites[ent_name] = {})

      if (enitites_of_type[p.tail])
        $me_throw(`${parent.path} already has ${ent_name} '${p.tail}'`)
      enitites_of_type[p.tail] = this
      this._active = parent._active
    }
    // if (p.ent == $me_atom2_entity_enum.elem || p.ent == $me_atom2_entity_enum.control) {
    // if (!$me_atom2_en.to_activate_candidates) $me_atom2.to_activate_candidates = new Set<$me_atom2_entity>()
    $me_atom2_entity._to_activate.add(this)
    // }
    this.met(this.path.ent, this.path.tail)
  }
  // static to_activate_candidates: Set<$me_atom2_entity>
  protected _on_active() {
  }
  destroy() {
    if (this.path.ent === $me_atom2_entity_enum.root)
      $me_throw(`can not destroy root`)
    this._active = false
    $me_atom2_entity._to_activate.delete(this)
    // this.active(false)
    // if (!$me_atom2.to_activate_candidates)
    if (this._entities) {
      for (const ent_name in this._entities) {
        const enitites_of_type = this._entities[ent_name]
        for (const name in enitites_of_type)
          enitites_of_type[name].destroy()
      }
    }
    const ent_name = $me_atom2_entity_enum[this.path.ent]
    const parent = this.parent()
    if (parent) {
      if (parent._entities[ent_name]) {
        delete parent._entities[ent_name][this.path.tail]
      } else {
        $me_throw(this.name(), parent.name(), parent._entities, (this._descendant(0) as $me_atom2_entity).name())
      }
    } else {
      $me_throw(this.name())
    }
    // this._parent_cache = null
  }
  by_path_s(s: string, keys?: $me_atom2_keys_intf): $me_atom2_entity | string {
    let parent = this as $me_atom2_entity
    let n = 0
    let ch = s.slice(0, 1)
    if (ch === $me_atom2_path_ent2prefix[$me_atom2_entity_enum.root]) {
      parent = $me_atom2_entity.root()
      n = 1
    } else if (ch === $me_atom2_path_ch_parent) {
      while (true) {
        n++
        ch = s.slice(n, n + 1)
        if (n >= s.length || ch !== $me_atom2_path_ch_parent) break
      }
      parent = this._descendant(n - 1, true) as $me_atom2_entity
      if (typeof parent === 'string')
        $me_throw(`${s}: ${parent}`)
    }
    const path = $me_atom2_path.fromString(s, n, keys) as $me_atom2_path
    if (typeof path === 'string')
      $me_throw(`${s}: ${path}`)
    const result = parent.by_path(path) || path.toString(parent.path)
    return result
  }
  by_path<Entity extends $me_atom2_entity=$me_atom2_entity>(path: $me_atom2_path): Entity {
    let result = null as Entity
    if (!path) {
      result = this as any as Entity
    } else if (path.ent === $me_atom2_entity_enum.root) {
      result = $me_atom2_entity.root() as any as Entity
    } else {
      const entity_parent = !path.path ? this : this.by_path(path.path)
      if (entity_parent) {
        const enitites = entity_parent._entities
        if (enitites) {
          const ent_name = $me_atom2_entity_enum[path.ent]
          const enitites_of_type = enitites[ent_name]
          result = enitites_of_type && enitites_of_type[path.tail] as Entity
        }
      }
    }
    return result
  }
  static _static_active: boolean
  static static_active(val?: boolean) {
    if (void 0 !== val) {
      // console.log(this.name(), val)
      $me_atom2_entity._static_active = val
      if (val) {
        // if ($me_atom2.to_activate_candidates) {
        //   for (const entity of $me_atom2.to_activate_candidates) {
        //     entity._on_active()
        //   }
        // }
        // for (const entity of $me_atom2.to_activate_candidates)
        //   $me_atom2._to_activate.add(entity)

        // $me_atom2._to_activate = $me_atom2.to_activate_candidates
        // $me_atom2.to_activate_candidates = null
        $me_atom2_async()
      }
    }
    return $me_atom2_entity._static_active
    // return $me_atom2_entity._static_active !== void 0 ? $me_atom2_entity._static_active : this.path.ent === $me_atom2_entity_enum.root
  }
  active(val?: boolean) {
    return this._active && $me_atom2_entity.static_active()
    // $me_atom2_entity_enum.root
    // if (void 0 !== val) {
    //   console.log(this.name(), val)
    //   $me_atom2_entity.static_active = val
    //   if (val) {
    //     if ($me_atom2.to_activate_candidates) {
    //       for (const entity of $me_atom2.to_activate_candidates) {
    //         entity._on_active()
    //       }
    //     }
    //     // $me_atom2._to_activate = $me_atom2.to_activate_candidates
    //     $me_atom2.to_activate_candidates = null
    //     $me_atom2_async()
    //   }
    // }
    // return $me_atom2_entity.static_active !== void 0 ? $me_atom2_entity.static_active : this.path.ent === $me_atom2_entity_enum.root
    // return this.path.ent === $me_atom2_entity_enum.root || $me_atom2_entity.static_active
    // if (this.entity_active_did) return true
    // return true
    // if (void 0 !== val) {
    //   val = val && (this.path.ent === $me_atom2_entity_enum.root || this.parent()._active)
    //   if (val !== this._active) {
    //     this._active = val
    //     for (const ent_name in this._entities) {
    //       const enitites_of_type = this._entities[ent_name]
    //       for (const tail in enitites_of_type) {
    //         // if (!enitites_of_type[tail].active) $me_throw(tail, this.name(), enitites_of_type) //console.warn(this.name(), tail)
    //         enitites_of_type[tail].active(val)
    //       }
    //     }
    //     if (val) {
    //       this._on_active()
    //       // this.entity_active_did = true
    //     }
    //   }
    // }
    // return this._active
  }
  // private entity_active_did: boolean
  parent(skip_keys = false) {
    const ret = this._descendant(0, skip_keys)
    if (!ret) $me_throw('no parent', this)
    if (typeof ret == 'string') $me_throw(ret as string, this)
    return ret as $me_atom2_entity
  }
  name() {
    return this.path.toString()
  }
  props(props: $me_atom2_prop_intf | $me_atom2_prop_intf[], pp?: {
    def?: (pp: {
      tail: string,
      idx: number,
      len: number,
      prop_defined: {[tail: string]: number},
      prop_def: $me_atom2_prop_def<any>,
      p: $me_atom2_prop_def_prepare_p_intf<any>,
    }) => $me_atom2_def<any>,
    dup?: (pp: {
      tail: string,
      idx: number,
      len: number,
      prop_defined: {[tail: string]: number},
      src: $me_atom2_prop_intf[],
    }) => void,
  }) {
    const prop_defined = <{[tail: string]: number}>{}
    if (props) {
      const src =
        Array.isArray(props) ?
        props as $me_atom2_prop_intf[] :
        [props as $me_atom2_prop_intf]
      let idx = 0
      const len = src.length
      for (const props of src) {
        if (props)
          for (const tail in props)
            if (props[tail])
              if (void 0 === prop_defined[tail]) {
                const prop_def = props[tail]
                const p = <$me_atom2_prop_def_prepare_p_intf<any>>{
                  parent: this,
                  tail,
                }
                let def_atom: $me_atom2_def<any>
                if (pp && pp.def )
                  def_atom = pp.def({tail, idx, len, prop_defined, prop_def, p})
                if (def_atom === null) continue
                if (def_atom === void 0)
                  def_atom = $me_atom2_prop_def_prepare<any>(prop_def, p)

                /// !!! EXPERIMENTAL
                if (false) {
                  const pp = def_atom as $me_atom2_def_compute_apply_intf<any>
                  if (!pp.fn_apply && Array.isArray(pp.masters)) {
                    const masters = pp.masters as string[]
                    const len = masters.length
                    if (len == 1) {
                      const master = (pp.masters as string[])[0]
                      if (master.indexOf('[') === -1) {
                        let conforms = master.startsWith('/')
                        let atom: $me_atom2
                        if (conforms) {
                          atom = $me_atom2_entity.root().by_path_s(master) as $me_atom2
                          conforms = atom instanceof $me_atom2
                        } else if (master.startsWith('.') && !pp.fn_compute) {
                          atom = pp.parent.by_path_s(master) as $me_atom2
                          conforms = (atom instanceof $me_atom2 && atom.path.path !== pp.parent.path)
                        }
                        if (conforms) {
                          if (!pp.parent._entities) pp.parent._entities = {}
                          if (!pp.parent._entities.prop) pp.parent._entities.prop = {}
                          pp.parent._entities.prop[pp.tail] = atom
                          prop_defined[tail] = idx
                          $me_atom2_entity_saved++
                          if (!$me_atom2_entity_saved_stat[tail]) $me_atom2_entity_saved_stat[tail] = 0
                          $me_atom2_entity_saved_stat[tail]++
                          continue
                        }
                      }
                    } else if (len > 0) {
                      let b = false
                      for (const master of masters) {
                        let conforms = master.startsWith('/')
                        let atom: $me_atom2
                        if (conforms) {
                          atom = $me_atom2_entity.root().by_path_s(master) as $me_atom2
                          conforms = atom instanceof $me_atom2
                        } else if (master.startsWith('.') && !pp.fn_compute) {
                          atom = pp.parent.by_path_s(master) as $me_atom2
                          conforms = (atom instanceof $me_atom2 && atom.path.path !== pp.parent.path)
                        }
                        if (!(b = conforms)) break
                      }
                      if (b) console.warn(pp)
                    }
                  }
                }

                $me_atom2.to_def.push(def_atom)
                this.wait($me_atom2_entity_enum.prop, tail)
                prop_defined[tail] = idx
              } else if (pp && pp.dup) {
                pp.dup({tail, idx, len, prop_defined, src})
              }
        idx++
      }
    }
    return prop_defined
  }
  // ===========================
  get 'parent()'() {
    return this.parent()
  }
  get 'name()'() {
    return this.name()
  }
  // toString() {
  //   return this.name()
  // }
  // ===========================
  // private
  _active: boolean
  // protected
  _entities: {
    [ent_name: string]: {
      [entity_name: string]: $me_atom2_entity
    }
  }
  // private _parent_cache: {
  //   [skip_keys: string]: $me_atom2_entity
  // }
  _descendant(level: number, skip_keys = false): $me_atom2_entity | string {
    // let cache_key: string
    // if (level === 0 && this._parent_cache && this._parent_cache[cache_key = skip_keys + '']) {
    //   return this._parent_cache[cache_key]
    // }
    let path = this.path
    let i = level + 1
    for (; i > 0 && path.ent !== $me_atom2_entity_enum.root; i--) {
      if (!skip_keys || path.ent !== $me_atom2_entity_enum.key) {
        path = path.path
      } else {
        while (path.ent === $me_atom2_entity_enum.key) path = path.path
        path = path.path
      }
    }
    if (i > 0)
      return `'${this.path}'._descendant(${!level ? '' : level}) is out of range (no parent above root)`
    const result = $me_atom2_entity.root().by_path(path)
    if (result == null)
      return `entity '${path}' not found`
    // if (level === 0) {
    //   if (!this._parent_cache) this._parent_cache = {}
    //   this._parent_cache[cache_key || skip_keys + ''] = result as $me_atom2_entity
    // }
    return result as $me_atom2_entity
  }
  // ===========================
  // private
  _waiting_for: Map<$me_atom2_entity_enum, Set<string>>
  wait(ent: $me_atom2_entity_enum, tail: string) {
    if (!this._waiting_for) this._waiting_for = new Map()
    if (!this._waiting_for.has(ent)) this._waiting_for.set(ent, new Set())
    this._waiting_for.get(ent).add(tail)

    // following code significally slows grid scroll:
    if (this.path.ent !== $me_atom2_entity_enum.root)
      this.parent().wait(this.path.ent, this.path.tail)

    $me_atom2.static_active(false)
    // this.active(false)
  }
  met(ent: $me_atom2_entity_enum, tail: string) {
    if (this._waiting_for) {
      if (this._waiting_for.has(ent)) {
        const ss = this._waiting_for.get(ent)
        ss.delete(tail)
        if (!ss.size) this._waiting_for.delete(ent)
        if (!this._waiting_for.size) this._waiting_for = null
      }
    }
    if (!this._waiting_for) {
      if (this.path.ent !== $me_atom2_entity_enum.root)
        this.parent().met(this.path.ent, this.path.tail)
      // if (this.path.ent == $me_atom2_entity_enum.root) console.error('heer')
      // $me_atom2_entity._to_activate.add(this.path)
      $me_atom2.static_active(true)
      $me_atom2_async()
    }
  }
  static activate_entities() {
    const root = $me_atom2_entity.root()
    let count = 0
    // console.warn($me_atom2_entity.static_active(), $me_atom2_entity._to_activate)
    if ($me_atom2_entity.static_active() && $me_atom2_entity._to_activate.size) {
      for (const entity of $me_atom2_entity._to_activate) {
        if (entity._waiting_for) continue
        if (entity._active) {
          entity._on_active()
          count++
        }
        $me_atom2_entity._to_activate.delete(entity)
      }
    }
    // for (const path of $me_atom2_entity._to_activate) {
    //   const entity = root.by_path(path)
    //   if (entity && !entity._waiting_for && !entity.active()) {
    //     const start = performance.now()
    //     entity.active(true)
    //     if ($me_debug) console.warn(entity.name(), performance.now() - start)
    //     count++
    //   }
    // }
    // const count = $me_atom2_entity._to_activate.size
    // $me_atom2_entity._to_activate.clear()
    return count
  }
  static _to_activate = new Set<$me_atom2_entity>()
}

