namespace $.$$ {
// ============================================================================

const accel_scrollAccu_threshold = 30

export abstract class $me_atom2_wheel_synth_class {
  mode: $me_atom2_wheel_synth_mode

  scrollAccuX = 0
  scrollAccuY = 0

  lastDeltaX = 0
  lastDeltaY = 0

  accelX = 0
  accelY = 0

  timePrevX: number
  timePrevY: number

  accel_k: number
  tPrev: number

  cancel() {
    this.mode = null
  }

  endHelper() {
    if (this.mode != $me_atom2_wheel_synth_mode.move) {
      this.cancel()
    } else {
      // this._end = event
      this.accel_k = 1
      this.mode = $me_atom2_wheel_synth_mode.end
      $me_atom2_async()
    }
  }
  // end(event: TouchEvent) {
  //   if (this.mode != $me_atom2_wheel_synth_mode.move) {
  //     this.cancel()
  //   } else {
  //     this._end = event
  //     this.accel_k = 1
  //     this.mode = $me_atom2_wheel_synth_mode.end
  //     $me_atom2_async()
  //   }
  // }

  raf(t: number) {
    // console.log(50, $me_atom2_wheel_synth_mode[this.mode])
    const result =
      this.mode == $me_atom2_wheel_synth_mode.move ||
      this.mode == $me_atom2_wheel_synth_mode.init ||
      false ?
        this.rafMove(t) :
      this.mode == $me_atom2_wheel_synth_mode.end ?
        this.rafEnd(t) :
        null
    // console.log(59, result, this.tPrev, t)
    return result
  }

  abstract rafMove(t: number): any
  abstract rafEnd(t: number): any

  rafMoveHeler(t: number) {
    // commented for metro.ts
    // if (Math.abs(this.scrollAccuY) > Math.abs(this.scrollAccuX)) {
    //   this.scrollAccuX = 0
    // } else {
    //   this.scrollAccuY = 0
    // }
    const timeCurr = performance.now()
    const accel_threshold = a('/.#wheelTouchAccelThreshold')
    if (Math.abs(this.scrollAccuX) < accel_threshold) {
      this.accelX = 0
    } else if (
      this.timePrevX != null &&
      Math.abs(this.scrollAccuX) >= accel_scrollAccu_threshold &&
      timeCurr != this.timePrevX &&
      true
    ) {
      // this.tPrev = t
      this.accelX = this.scrollAccuX / (timeCurr - this.timePrevX)
      this.timePrevX = timeCurr
    }
    if (Math.abs(this.scrollAccuY) < accel_threshold) {
      this.accelY = 0
    } else if (
      this.timePrevY != null &&
      Math.abs(this.scrollAccuY) >= accel_scrollAccu_threshold &&
      timeCurr != this.timePrevY &&
      true
    ) {
      // this.tPrev = t
      this.accelY = this.scrollAccuY / (timeCurr - this.timePrevY)
      this.timePrevY = timeCurr
    }
    this.tPrev = t
    // console.log(97, this.scrollAccuY)
    const result = {
      mode: this.mode,
      _deltaX: this.scrollAccuX,
      _deltaY: this.scrollAccuY,
    }
    this.scrollAccuX = 0
    this.scrollAccuY = 0
    return result
  }
}

// ==========================================================================
}
