    //   true
    // ) {
    //   this.tPrev = t
    //   this.accelY = this.scrollAccuY / (timeCurr - this.timePrevY)
    //   this.timePrevY = timeCurr
    // }
    // const result = <$me_atom2_event_wheel_touch_intf>{
